export async function getSampleBallot(
  cluster: string,
  barangayId: string
): Promise<string | undefined> {
  let parameters: string = `?barangayId=${barangayId}&cluster=${cluster}`;
  return await httpGet<string>(VoterEnd.PrintSampleBallot + parameters);
}
