export async function getDistributionReceivingByDate(
  cluster: string,
  barangayId: string,
  date: Date,
  check: boolean
): Promise<string | undefined> {
  let parameters: string = `?barangayId=${barangayId}&cluster=${cluster}&check=${check}`;
  if (date !== null) {
    parameters += `&date=${encodeURIComponent(date.toUTCString())}`;
  }
  return await httpGet<string>(
    VoterEnd.PrintDistributionReceivingByDate + parameters
  );
}

