export async function searchVoter(
  key: string | undefined,
  page: number,
  itemCount?: number,
  withPositionOnly?: boolean,
  withoutPositionOnly?: boolean
): Promise<VoterSearch | undefined> {
  let parameters: string = '?page=' + page;
  if (key !== undefined) {
    parameters += '&key=' + encodeURI(key);
  }
  if (itemCount !== undefined) {
    parameters += '&itemCount=' + itemCount;
  }
  if (withPositionOnly !== undefined) {
    parameters += '&withPositionOnly=' + withPositionOnly;
  }
  if (withoutPositionOnly !== undefined) {
    parameters += '&withoutPositionOnly=' + withoutPositionOnly;
  }
  console.log(parameters);
  return await httpGet<VoterSearch>(VoterEnd.Search + parameters);
}

