export async function searchVoters(
  name?: string,
  positionId?: string,
  district?: string,
  barangayId?: string,
  precinct?: string,
  precinctStart?: string,
  precinctEnd?: string,
  cluster?: string,
  noBcc?: boolean,
  noPurok?: boolean,
  noLiner?: boolean,
  noCoding?: boolean,
  page?: number,
  itemCount?: number
): Promise<VoterSearch | undefined> {
  let parameters: string = '?page=' + page ?? 1;
  if (name !== undefined) {
    parameters += '&name=' + encodeURI(name);
  }
  if (positionId !== undefined) {
    parameters += '&positionId=' + positionId;
  }
  if (district !== undefined) {
    parameters += '&district=' + district;
  }
  if (barangayId !== undefined) {
    parameters += '&barangayId=' + barangayId;
  }
  if (precinct !== undefined) {
    parameters += '&precinct=' + encodeURI(precinct);
  }
  if (precinctStart !== undefined) {
    parameters += '&precinctStart=' + encodeURI(precinctStart);
  }
  if (precinctEnd !== undefined) {
    parameters += '&precinctEnd=' + encodeURI(precinctEnd);
  }
  if (cluster !== undefined) {
    parameters += '&cluster=' + cluster;
  }
  if (noBcc ?? false) {
    parameters += '&noBcc=true';
  }
  if (noPurok ?? false) {
    parameters += '&noPurok=true';
  }
  if (noLiner ?? false) {
    parameters += '&noLiner=true';
  }
  if (noCoding ?? false) {
    parameters += '&noCoding=true';
  }
  if (itemCount !== undefined) {
    parameters += '&itemCount=' + itemCount;
  }
  return await httpGet<VoterSearch>(VoterEnd.SearchVoters + parameters);
}

