import { IconProp } from '@fortawesome/fontawesome-svg-core';
import { faPlus, faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useEffect, useReducer, useState } from 'react';
import {
  useSetBusy,
  useSetMessage,
} from '../../custom-hooks/authorize-provider';
import { VoterData } from '../../entities/voter/VoterData';
import { VoterPosition } from '../../entities/voter/VoterPosition';
import {
  deleteVoterInTree,
  getVotersMembers,
  insertVoterInTree,
  searchForMembers,
} from '../../processors/voter';
import { getVoterPositions } from '../../processors/voter-position';
import PositionLeader from '../components/position-leader';
import SeachBar from '../components/seachbar';
import VoterRemarks from '../components/voter-remarks';
import { POSITION } from '../group-management-page';
import Modal from './modal';

type VoterAction =
  | {
      type: 'Delete';
      payload: number;
    }
  | {
      type: 'Fill';
      payload: VoterData[] | undefined;
    }
  | {
      type: 'Clear';
    };

type MemberAction =
  | {
      type: 'Add';
      payload: VoterData;
    }
  | {
      type: 'Delete';
      payload: number;
    }
  | {
      type: 'Fill';
      payload: VoterData[] | undefined;
    }
  | {
      type: 'Clear';
    };

function memberReducer(state: VoterData[], action: MemberAction): VoterData[] {
  switch (action.type) {
    case 'Fill':
      return [...state, ...(action.payload ?? [])];
    case 'Clear':
      return [];
    case 'Delete':
      return state.filter((x) => x.id !== action.payload);
    case 'Add':
      return [...state, action.payload].sort((x, y) =>
        (x.fullName ?? '') > (y.fullName ?? '') ? 1 : -1
      );
    default:
      return state;
  }
}
function voterReducer(state: VoterData[], action: VoterAction): VoterData[] {
  switch (action.type) {
    case 'Fill':
      return [...state, ...(action.payload ?? [])];
    case 'Clear':
      return [];
    case 'Delete':
      return state.filter((x) => x.id !== action.payload);
    default:
      return state;
  }
}
export default function VoterMemberManagement({
  headId,
  voterPositionLevel,
  onClose,
  position,
}: {
  headId?: number;
  voterPositionLevel: number | undefined;
  onClose: (hasChange: boolean) => void;
  position: POSITION | undefined;
}) {
  const [positions, setPositions] = useState<VoterPosition[]>([]);
  const [total, setTotal] = useState(0);
  const [hasChange, setHasChange] = useState(false);
  const [searchKey, setSearchKey] = useState<string | undefined>();
  const [voterPageCount, setVoterPageCount] = useState<number>(1);
  const [currentVoterPage, setCurrentVoterPage] = useState<number>(1);
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();
  const [membersList, dispatchMember] = useReducer(memberReducer, []);
  const [votersList, dispatchVoter] = useReducer(voterReducer, []);
  useEffect(
    () => {
      getPositions();
    },
    // eslint-disable-next-line
    []
  );

  useEffect(() => {
    setHasChange(true);
    return () => {
      setHasChange(false);
    };
  }, [total]);

  useEffect(
    () => {
      getMember(1);
      return () => {
        dispatchMember({ type: 'Clear' });
      };
    }, // eslint-disable-next-line
    [headId, voterPositionLevel]
  );

  function getPositions() {
    setBusy(true);
    getVoterPositions()
      .then((res) => {
        if (res !== undefined) {
          setPositions(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  async function searchVotersList(key: string | undefined, page: number) {
    setBusy(true);
    await searchForMembers(key, voterPositionLevel ?? 0, headId, page, 50)
      .then((res) => {
        if (res !== undefined) {
          dispatchVoter({ type: 'Fill', payload: res.voters });
          setVoterPageCount(res.pageCount);
          setCurrentVoterPage(page);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function getMember(page: number) {
    setBusy(true);
    await getVotersMembers(voterPositionLevel ?? -1, page, headId, 10000000)
      .then((res) => {
        if (res !== undefined) {
          dispatchMember({ type: 'Fill', payload: res.voters });
          setTotal(res.total);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function searchVoters(key: string) {
    setSearchKey(key);
    dispatchVoter({ type: 'Clear' });
    searchVotersList(key, 1);
  }

  function loadMoreVoter() {
    searchVotersList(searchKey, currentVoterPage + 1);
  }

  function addToVoter(voter: VoterData): void {
    setBusy(true);
    insertVoterInTree({
      votersId: voter.id,
      votersHeadId: headId,
      positionId: positions.filter((x) => x.level === voterPositionLevel)[0].id,
    })
      .then(async (res) => {
        if (res !== undefined) {
          setTotal((t) => t + 1);
          if (voter.position?.name === null) {
            voter.position = positions.filter(
              (x) => x.level === voterPositionLevel
            )[0];
            voter.positionId = voter.position?.id ?? 0;
          } else {
            voter.position2 = positions.filter(
              (x) => x.level === voterPositionLevel
            )[0];
            voter.positionId2 = voter.position2?.id ?? 0;
          }
          dispatchMember({ type: 'Add', payload: voter });
          dispatchVoter({ type: 'Delete', payload: voter.id });
          setHasChange(true);
          // setMessage({ message: voter.fullName + ' added.' });
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function deleteVoter(voter: VoterData): void {
    setMessage({
      message: 'Delete ' + voter.fullName,
      type: 'MESSAGE',
      action: 'YESNO',
      onOk: () => {
        setBusy(true);
        deleteVoterInTree(voter.id, headId)
          .then(async (res) => {
            if (res !== undefined) {
              setTotal((t) => t - 1);
              dispatchMember({ type: 'Delete', payload: voter.id });
              setHasChange(true);
              // setMessage({ message: voter.fullName + ' deleted.' });
            }
          })
          .catch((err) => {
            setMessage({ message: err.message });
          })
          .finally(() => setBusy(false));
      },
    });
  }
  return (
    <Modal
      className='management-modal'
      onClose={() => onClose(hasChange)}
      title={position + ' MANAGEMENT'}>
      <div className='main-item-container full-modal-body'>
        <div className='item-container div-container no-padding-top-container'>
          <SeachBar search={searchVoters} />
          <div className='item-container voters-container'>
            <table className='item-table'>
              <thead>
                <tr>
                  <th></th>
                  <th>Name</th>
                  <th>Position</th>
                  <th>Barangay</th>
                  <th>Precinct</th>
                </tr>
              </thead>
              <tbody>
                {votersList.map((voter) => (
                  <tr key={voter.id} id={'voter-' + voter.id}>
                    <td>
                      {voter.position?.name === null && (
                        <FontAwesomeIcon
                          className='add-icon table-icon-button'
                          icon={faPlus as IconProp}
                          onClick={() => addToVoter(voter)}
                        />
                      )}
                    </td>
                    <td>{voter.fullName}</td>
                    <td>
                      <PositionLeader
                        position={voter.position}
                        leader={
                          voter.position?.tag === 'PN'
                            ? voter.pnLeader
                            : voter.jtcLeader
                        }
                        doublePosition={voter.position2?.name !== null}
                      />
                      <PositionLeader
                        position={voter.position2}
                        leader={
                          voter.position2?.tag === 'PN'
                            ? voter.pnLeader
                            : voter.jtcLeader
                        }
                        doublePosition={true}
                        pending={
                          voter.position2?.level === 5 ||
                          voter.position2?.level === 10
                        }
                      />
                    </td>
                    <td>{voter.barangay?.name}</td>
                    <td>{voter.precinctNumber}</td>
                  </tr>
                ))}
              </tbody>
              {currentVoterPage !== voterPageCount && voterPageCount > 0 && (
                <tfoot>
                  <tr onClick={loadMoreVoter}>
                    <td colSpan={5} className='load-more' align='center'>
                      Load More
                    </td>
                  </tr>
                </tfoot>
              )}
            </table>
          </div>
        </div>
        <div className='item-container div-container no-padding-top-container'>
          <div className='voter-header'>{position}</div>
          <div className='item-container voters-container'>
            <table className='item-table'>
              <thead>
                <tr>
                  <th></th>
                  <th>Name</th>
                  <th>Barangay</th>
                  <th>Precinct</th>
                  <th>Remarks</th>
                </tr>
              </thead>
              <tbody>
                {membersList.map((member) => (
                  <tr key={member.id} id={'member-' + member.id}>
                    <td>
                      <FontAwesomeIcon
                        className='remove-icon table-icon-button'
                        icon={faTimes as IconProp}
                        onClick={() => deleteVoter(member)}
                      />
                    </td>
                    <td>{member.fullName}</td>
                    <td>{member.barangay?.name}</td>
                    <td>{member.precinctNumber}</td>
                    <td>
                      <VoterRemarks voter={member} />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className='footer'>
            <div className='total'>Total Count : {total.toLocaleString()}</div>
          </div>
        </div>
      </div>
    </Modal>
  );
}
