import { useEffect, useState } from 'react';
import {
  useSetBusy,
  useSetMessage,
} from '../../custom-hooks/authorize-provider';
import { Barangay } from '../../entities/voter/Barangay';
import { Sex } from '../../entities/voter/Sex';
import { VoterFullData } from '../../entities/voter/VoterFullData';
import { VoterStatus } from '../../entities/voter/VoterStatus';
import { getBarangays } from '../../processors/barangay';
import { getSexes } from '../../processors/sex';
import { createVoter, updateVoter } from '../../processors/voter';
import { getVoterStatuses } from '../../processors/voter-status';
import CustomDatePicker from '../components/custom-datepicker';
import CustomDropdown, { DropdownItem } from '../components/custom-dropdown';
import CustomTextBox from '../components/custom-textbox';
import { CustomReturn } from '../components/CustomReturn';
import Modal from './modal';

export default function ManageVoter({
  votersInfo,
  onCloseModal,
}: {
  votersInfo?: VoterFullData;
  onCloseModal: (hasChange: boolean) => void;
}) {
  const [voter, setVoter] = useState<VoterFullData>(
    () =>
      votersInfo ?? {
        id: 0,
        sexId: 1,
        barangayId: 1,
        statusId: 1,
      }
  );
  const [sexes, setSexes] = useState<Sex[]>([]);
  const [barangays, setBarangays] = useState<Barangay[]>([]);
  const [voterStatus, setVoterStatus] = useState<VoterStatus[]>([]);
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();

  useEffect(
    () => {
      initializeComponents();
    },
    // eslint-disable-next-line
    []
  );

  async function initializeComponents() {
    await getSex();
    await getBarangay();
    await getVoterStatus();
  }

  async function getSex() {
    setBusy(true);
    await getSexes()
      .then((res) => {
        if (res !== undefined) {
          setSexes(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  async function getBarangay() {
    setBusy(true);
    await getBarangays()
      .then((res) => {
        if (res !== undefined) {
          setBarangays(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  async function getVoterStatus() {
    setBusy(true);
    await getVoterStatuses()
      .then((res) => {
        if (res !== undefined) {
          setVoterStatus(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function onChange({ elementName, value }: CustomReturn) {
    setVoter((prevVoter) => {
      if (
        ['barangayId', 'sexId', 'statusId'].find((x) => x === elementName) !==
        undefined
      ) {
        return { ...prevVoter, [elementName]: parseInt(value) };
      } else if (elementName === 'birthDate') {
        return { ...prevVoter, [elementName]: new Date(value) };
      }
      return { ...prevVoter, [elementName]: value };
    });
  }
  async function saveData() {
    if ((voter.lastName ?? '') === '') {
      setMessage({ message: 'Invalid last name' });
      return;
    } else if ((voter.firstName ?? '') === '') {
      setMessage({ message: 'Invalid first name' });
      return;
    } else if ((voter.sexId ?? 0) === 0) {
      setMessage({ message: 'Invalid sex' });
      return;
    } else if (voter.birthDate === undefined) {
      setMessage({ message: 'Invalid birth date' });
      return;
    } else if ((voter.barangayId ?? 0) === 0) {
      setMessage({ message: 'Invalid barangay' });
      return;
    } else if ((voter.precinctNumber ?? '') === '') {
      setMessage({ message: 'Invalid precinct no.' });
      return;
    } else if ((voter.statusId ?? 0) === 0) {
      setMessage({ message: 'Invalid status' });
      return;
    }

    setBusy(true);
    if (voter.id === 0) {
      await createVoter(voter)
        .then(() => {
          setMessage({
            message: 'New Voter Added',
            onOk: () => {
              onCloseModal(false);
            },
          });
        })
        .catch((err) => {
          setMessage({ message: err.message });
        })
        .finally(() => setBusy(false));
    } else {
      await updateVoter(voter)
        .then(() => {
          setMessage({
            message: 'Voter Updated',
            onOk: () => {
              onCloseModal(true);
            },
          });
        })
        .catch((err) => {
          setMessage({ message: err.message });
        })
        .finally(() => setBusy(false));
    }
  }

  return (
    <Modal
      className='voter-modal'
      onClose={() => onCloseModal(false)}
      title={
        (voter?.id ?? 0) > 0 ? 'Update Voters Information' : 'Add New Voter'
      }>
      <div className='modal-form'>
        <CustomTextBox
          title='Last Name'
          name='lastName'
          readonly={true}
          value={voter?.lastName}
          onChange={onChange}
        />
        <CustomTextBox
          title='First Name'
          name='firstName'
          readonly={true}
          value={voter?.firstName}
          onChange={onChange}
        />
        <CustomTextBox
          title='Middle Name'
          name='middleName'
          readonly={true}
          value={voter?.middleName}
          onChange={onChange}
        />
        <CustomTextBox
          title='Name Extension'
          name='nameExtension'
          readonly={true}
          value={voter?.nameExtension}
          onChange={onChange}
        />
        <CustomDropdown
          title='Sex'
          name='sexId'
          readonly={true}
          value={voter?.sexId?.toString()}
          onChange={onChange}
          itemsList={sexes.map<DropdownItem>((s) => {
            return { key: s.id?.toString() ?? '', value: s.name ?? '' };
          })}
        />
        <CustomDatePicker
          title='Birth Date'
          name='birthDate'
          readonly={true}
          value={
            voter?.birthDate === undefined
              ? undefined
              : new Date(voter?.birthDate ?? new Date())
          }
          onChange={onChange}
        />
        <CustomDropdown
          title='Barangay'
          readonly={true}
          name='barangayId'
          value={voter?.barangayId?.toString()}
          onChange={onChange}
          itemsList={barangays.map<DropdownItem>((s) => {
            return { key: s.id?.toString() ?? '', value: s.name ?? '' };
          })}
        />
        <CustomTextBox
          title='Precinct'
          name='precinctNumber'
          readonly={true}
          value={voter?.precinctNumber}
          onChange={onChange}
        />
        <CustomTextBox
          title='Contact No.'
          name='contactNumber'
          value={voter?.contactNumber}
          onChange={onChange}
        />
        <CustomDropdown
          title='Status'
          name='statusId'
          readonly={true}
          value={voter?.statusId?.toString()}
          onChange={onChange}
          itemsList={voterStatus.map<DropdownItem>((s) => {
            return { key: s.id?.toString() ?? '', value: s.name ?? '' };
          })}
        />
      </div>
      <div className='modal-footer'>
        <button onClick={saveData} className='btn-modal btn-primary'>
          SAVE
        </button>
      </div>
    </Modal>
  );
}
