import React, { useState } from 'react';
import DateTimePicker from 'react-datetime-picker';
import { useSetBusy, useSetMessage } from '../custom-hooks/authorize-provider';
import { Attendance } from '../entities/voter/Attendance';
import { printAttendanceReport } from '../pdf-report.ts/report';
import { getAttendance } from '../processors/voter';

export default function AttendanceReportPage() {
  const [dateStart, setDateStart] = useState<Date>(() => new Date());
  const [dateEnd, setDateEnd] = useState<Date>(() => new Date());
  const [attendances, setAttendances] = useState<Attendance[]>([]);
  // const [clusters, setClusters] = useState<DropdownItem[]>([]);
  // const [selectedCluster, setSelectedCluster] = useState<string>();

  const setMessage = useSetMessage();
  const setBusy = useSetBusy();

  // function onChange({ value }: CustomReturn) {
  //   setSelectedCluster(() => value);
  // }

  // useEffect(
  //   () => {
  //     initializeComponents();
  //   },
  //   // eslint-disable-next-line
  //   []
  // );

  // async function initializeComponents() {
  //   await fetchCluster();
  // }

  // async function fetchCluster() {
  //   await getClusters()
  //     .then((res) => {
  //       if (res !== undefined) {
  //         setClusters([
  //           {
  //             key: '',
  //             value: '',
  //           },
  //           ...res.map((b) => {
  //             return {
  //               key: b.id.toString(),
  //               value: b.description ?? '',
  //             };
  //           }),
  //         ]);
  //       }
  //     })
  //     .catch((err) => {
  //       setMessage({ message: err.message });
  //     });
  // }
  async function search() {
    setBusy(true);
    await getAttendance(dateStart, dateEnd)
      .then((res) => {
        if (res !== undefined) {
          setAttendances(() => res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .then(() => {
        setBusy(false);
      });
  }
  function print() {
    printAttendanceReport(attendances);
  }
  return (
    <div className='main-container'>
      <div className='item-container no-padding-top-container'>
        <div className='report-filter-small'>
          <DateTimePicker
            className='datepicker'
            onChange={setDateStart}
            value={dateStart}
            disableCalendar={true}
            disableClock={true}
          />
          <DateTimePicker
            className='datepicker'
            onChange={setDateEnd}
            value={dateEnd}
            disableCalendar={true}
            disableClock={true}
          />
          {/* <CustomDropdown
            title='Cluster'
            name='cluster'
            value={selectedCluster}
            itemsList={clusters}
            onChange={onChange}
          /> */}
          <div className='btn-container'>
            <button onClick={search} className='btn'>
              Search
            </button>
          </div>
        </div>
      </div>
      <div className='item-container report-content-small-header'>
        <table className='report-table item-table'>
          <thead>
            <tr>
              <th></th>
              <th>Full Name</th>
              <th>Precinct</th>
              <th>Barangay</th>
              <th>Date &amp; Time In</th>
              <th>Signature</th>
            </tr>
          </thead>
          <tbody>
            {attendances.map((attendance, i) => (
              <tr key={attendance.id}>
                <td>{i + 1}</td>
                <td>{attendance.voter.fullName}</td>
                <td>{attendance.voter.precinctNumber}</td>
                <td>{attendance.voter.barangay?.name}</td>
                <td>
                  {attendance.loginDate &&
                    new Date(attendance.loginDate).toLocaleString()}
                </td>
                <td>
                  <img
                    src={attendance.voter?.signature}
                    height={70}
                    width={200}
                    alt='Signature'
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className='item-container report-footer'>
        <div className='count'>
          Total: {attendances.length?.toLocaleString()}
        </div>
        <div className='member-actions'>
          <button className='btn' onClick={print}>
            Print
          </button>
        </div>
      </div>
    </div>
  );
}
