import React, { useState } from 'react';
import Webcam from 'react-webcam';
import Modal from './modal';
import CustomDropdown from '../components/custom-dropdown';
import { CustomReturn } from '../components/CustomReturn';

export default function CaptureVoterImage({
  onSave,
  onClose,
  isPng,
}: {
  onSave: (image: string) => void;
  onClose: () => void;
  isPng?: boolean;
}) {
  const webcamRef = React.useRef<any>(null);
  const [deviceId, setDeviceId] = useState<string>();
  const [devices, setDevices] = useState<any>();
  function save() {
    onSave(webcamRef.current?.getScreenshot({ height: 130, width: 130 }));
  }

  const handleDevices = React.useCallback(
    (mediaDevices) =>
      setDevices(
        mediaDevices
          .filter(({ kind }: any) => kind === 'videoinput')
          .map((dev: any) => {
            return {
              key: dev.deviceId.toString(),
              value: dev.label.toString(),
            };
          })
      ),
    [setDevices]
  );

  React.useEffect(() => {
    navigator.mediaDevices.enumerateDevices().then(handleDevices);
  }, [handleDevices]);
  function changeDevice({ value }: CustomReturn) {
    console.log();
    setDeviceId(() => value);
  }
  return (
    <Modal onClose={onClose}>
      <CustomDropdown
        title='Devices'
        itemsList={devices}
        name='sevices'
        value={deviceId}
        onChange={changeDevice}
      />
      <div className='webcam-container'>
        <Webcam
          height={400}
          ref={webcamRef}
          imageSmoothing={true}
          screenshotFormat={isPng ? 'image/png' : 'image/jpeg'}
          width={400}
          videoConstraints={{
            deviceId: deviceId,
          }}
        />
      </div>
      <div className='modal-footer center-footer'>
        <button className='btn' onClick={save}>
          Capture Photo
        </button>
      </div>
    </Modal>
  );
}
