import React, { useEffect, useState } from 'react';
import { useSetBusy, useSetMessage } from '../custom-hooks/authorize-provider';
import { BarangayVote } from '../entities/voter/BarangayVote';
import { getVotersVote } from '../processors/voter';
import Statistics from './components/statistics';

export default function VotingStatisticsPage() {
  const [statistics, setStatistics] = useState<BarangayVote[]>([]);
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();

  async function getVotes() {
    setBusy(true);
    await getVotersVote()
      .then((res) => {
        if (res !== undefined) {
          setStatistics(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .then(() => {
        setBusy(false);
      });
  }

  useEffect(
    () => {
      getVotes();
    },
    // eslint-disable-next-line
    []
  );

  return (
    <div>
      {statistics.map((barangay) => (
        <Statistics key={barangay.id} barangay={barangay} />
      ))}
    </div>
  );
}
