import React, { useEffect, useState } from 'react';
import {
  useCurrentVoterId,
  useSetBusy,
  useSetCurrentVoterId,
  useSetMessage,
} from '../custom-hooks/authorize-provider';
import { VoterFullData } from '../entities/voter/VoterFullData';
import {
  getVotersCount,
  getVotersHeads,
  getVotersInfo,
} from '../processors/voter';
import { VoterHeads } from '../entities/voter/VoterHeads';
import { VotersCount } from '../entities/voter/VotersCount';
import { faPen, faSearch } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import SearchVoterModal from './modals/search-voter-modal';
import { positionLevel } from '../constant';
import VoterMember from './components/voter-member';
import ManageVoter from './modals/manage-voter';
import { IconProp } from '@fortawesome/fontawesome-svg-core';
import { VoterData } from '../entities/voter/VoterData';
export type POSITION =
  | 'BARANGAY COORDINATOR'
  | 'PUROK LEADER'
  | 'LINER'
  | 'CODING'
  | '';
export default function GroupManagementPage() {
  const [id, setId] = useState<number>();
  const [voter, setVoter] = useState<VoterFullData | undefined>();
  const [heads, setHeads] = useState<VoterHeads | undefined>();
  const [count, setCount] = useState<VotersCount | undefined>();
  const [selectedVoter, setSelectedVoter] = useState<
    VoterFullData | undefined
  >();
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();
  const [showModal, setShowModal] = useState(false);
  const [selectedTab, setSelectedTab] = useState<POSITION>('');
  const [showBCC, setShowBCC] = useState(false);
  const [showPurok, setShowPurok] = useState(false);
  const [showLiner, setShowLiner] = useState(false);
  const [showCoding, setShowCoding] = useState(false);
  const [showManageModal, setShowManageModal] = useState(false);
  const currentVoterId = useCurrentVoterId();
  const setCurrentVoterId = useSetCurrentVoterId();
  useEffect(
    () => {
      initializeComponents();
    },
    // eslint-disable-next-line
    [id]
  );

  useEffect(() => {
    setId(currentVoterId);
  }, [currentVoterId]);

  async function initializeComponents() {
    if (!isNaN(id ?? 0)) {
      await getVoter(id ?? 0);
      await getCounts();
    }
  }

  async function getVoter(votersId: number, voterOnly?: boolean) {
    setBusy(true);
    await getVotersInfo(votersId)
      .then(async (res) => {
        if (res !== undefined) {
          if (!(voterOnly ?? false)) {
            getMembers(
              (res?.position?.tag === 'PN'
                ? res?.position?.level
                : res?.position2?.level) ?? 0
            );
            await getHeads(res?.id ?? 0);
          }
          setVoter(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  async function getHeads(votersId: number) {
    setBusy(true);
    await getVotersHeads(votersId)
      .then((res) => {
        if (res !== undefined) {
          setHeads(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function getMembers(level: number) {
    setShowBCC(false);
    setShowPurok(false);
    setShowLiner(false);
    setShowCoding(false);

    setSelectedTab('');

    switch (level) {
      case 1:
        setShowBCC(true);
        setSelectedTab('BARANGAY COORDINATOR');
        break;
      case 2:
        setShowPurok(true);
        setSelectedTab('PUROK LEADER');
        break;
      case 3:
        setShowLiner(true);
        setSelectedTab('LINER');
        break;
      case 4:
        setSelectedTab('CODING');
        break;
    }
    if (level < 5 && level > 0) {
      setShowCoding(true);
    }
  }

  async function getCounts() {
    setBusy(true);
    await getVotersCount()
      .then((res) => {
        if (res !== undefined) {
          setCount(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function onClose() {
    setShowModal(false);
  }

  function selectSearchVoter(Voter: VoterData) {
    selectVoter(Voter?.id);
    setShowModal(false);
  }

  function selectVoter(votersId: number) {
    if (id === 0) return;
    setCurrentVoterId(votersId);
    getVoter(votersId);
  }

  function onManageClose(hasChanges: boolean) {
    setShowManageModal(false);
    if (hasChanges) {
      getVoter(voter?.id ?? 0, true);
    }
  }
  function hasChange() {
    getCounts();
  }
  return (
    <div className='main-container'>
      <div className='main-item-container'>
        <div className='div-container item-container '>
          <div className='voters-info'>
            <div className='actions'>
              <FontAwesomeIcon
                icon={faSearch as IconProp}
                className='btn-icon'
                onClick={() => setShowModal(true)}
              />
              {/* <FontAwesomeIcon
                className='btn-icon'
                icon={faPlus}
                onClick={() => {
                  setSelectedVoter(undefined);
                  setShowManageModal(true);
                }}
              /> */}
              <FontAwesomeIcon
                className='btn-icon'
                icon={faPen as IconProp}
                onClick={() => {
                  setSelectedVoter(voter);
                  setShowManageModal(true);
                }}
              />
            </div>
            <div className='voters-name'>{voter?.fullName ?? 'N/A'}</div>
            <div className='other-info'>
              <div>
                <div>Position:</div>
                <div>
                  {voter?.position?.tag === 'PN'
                    ? voter?.position?.name
                    : voter?.position2?.name}
                </div>
              </div>
              <div>
                <div>District:</div>
                <div>{voter?.barangay?.district}</div>
              </div>
              <div>
                <div>Barangay:</div>
                <div>{voter?.barangay?.name}</div>
              </div>
              <div>
                <div>Precinct Number:</div>
                <div>{voter?.precinctNumber}</div>
              </div>
              <div>
                <div>Birth Date:</div>
                <div>
                  {voter?.birthDate &&
                    new Date(voter.birthDate).toLocaleDateString()}
                </div>
              </div>
              <div>
                <div>Sex:</div>
                <div>{voter?.sex?.name}</div>
              </div>
              <div>
                <div>Contact Number:</div>
                <div>{voter?.contactNumber}</div>
              </div>
            </div>
          </div>
          <div className='heads-info'>
            <div>
              <div>DISTRICT COORDINATOR:</div>
              <div
                className={heads?.dcc?.fullName || 'disabled'}
                onDoubleClick={() => {
                  selectVoter(heads?.dcc?.id ?? 0);
                }}>
                {heads?.dcc?.fullName}
              </div>
            </div>
            <div>
              <div>BARANGAY COORDINATOR:</div>
              <div
                onDoubleClick={() => {
                  selectVoter(heads?.bcc?.id ?? 0);
                }}
                className={heads?.bcc?.fullName || 'disabled'}>
                {heads?.bcc?.fullName}
              </div>
            </div>
            <div>
              <div>PUROK LEADER:</div>
              <div
                onDoubleClick={() => {
                  selectVoter(heads?.purok?.id ?? 0);
                }}
                className={heads?.purok?.fullName || 'disabled'}>
                {heads?.purok?.fullName}
              </div>
            </div>
            <div>
              <div>LINER:</div>
              <div
                onDoubleClick={() => {
                  selectVoter(heads?.liner?.id ?? 0);
                }}
                className={heads?.liner?.fullName || 'disabled'}>
                {heads?.liner?.fullName}
              </div>
            </div>
          </div>
          <div className='total-info'>
            <table>
              <thead>
                <tr>
                  <th>Position</th>
                  <th>District 1</th>
                  <th>District 2</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>DISTRICT COORDINATOR</td>
                  <td>{count?.dcC1?.toLocaleString()}</td>
                  <td>{count?.dcC2?.toLocaleString()}</td>
                </tr>
                <tr>
                  <td>BARANGAY COORDINATOR</td>
                  <td>{count?.bcC1?.toLocaleString()}</td>
                  <td>{count?.bcC2?.toLocaleString()}</td>
                </tr>
                <tr>
                  <td>PUROK LEADER</td>
                  <td>{count?.purok1?.toLocaleString()}</td>
                  <td>{count?.purok2?.toLocaleString()}</td>
                </tr>
                <tr>
                  <td>LINER</td>
                  <td>{count?.liner1?.toLocaleString()}</td>
                  <td>{count?.liner2?.toLocaleString()}</td>
                </tr>
                <tr>
                  <td>CODING</td>
                  <td>{count?.member1?.toLocaleString()}</td>
                  <td>{count?.member2?.toLocaleString()}</td>
                </tr>
                <tr>
                  <td>PENDING CODING</td>
                  <td>{count?.pMember1?.toLocaleString()}</td>
                  <td>{count?.pMember2?.toLocaleString()}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className='total-count'>
            <div>Total Count:</div>
            <div>{count?.totalCount?.toLocaleString()}</div>
          </div>
        </div>
        {selectedTab !== '' && (
          <div className='div-container item-container tab-container'>
            <div className='tab-headers'>
              {showBCC && (
                <div
                  className={
                    selectedTab === 'BARANGAY COORDINATOR' ? 'selected' : ''
                  }
                  onClick={() => setSelectedTab('BARANGAY COORDINATOR')}>
                  BARANGAY COORDINATOR
                </div>
              )}
              {showPurok && (
                <div
                  className={selectedTab === 'PUROK LEADER' ? 'selected' : ''}
                  onClick={() => setSelectedTab('PUROK LEADER')}>
                  PUROK LEADER
                </div>
              )}
              {showLiner && (
                <div
                  className={selectedTab === 'LINER' ? 'selected' : ''}
                  onClick={() => setSelectedTab('LINER')}>
                  LINER
                </div>
              )}
              {showCoding && (
                <div
                  className={selectedTab === 'CODING' ? 'selected' : ''}
                  onClick={() => setSelectedTab('CODING')}>
                  CODING
                </div>
              )}
            </div>
            <div className='tab-contents'>
              {showBCC && (
                <div
                  className={
                    selectedTab === 'BARANGAY COORDINATOR'
                      ? 'selected-content'
                      : 'tab-content'
                  }>
                  <VoterMember
                    selectVoter={selectVoter}
                    voterPositionLevel={positionLevel.bcc}
                    headId={id ?? 0}
                    onHasChange={hasChange}
                  />
                </div>
              )}
              {showPurok && (
                <div
                  className={
                    selectedTab === 'PUROK LEADER'
                      ? 'selected-content'
                      : 'tab-content'
                  }>
                  <VoterMember
                    selectVoter={selectVoter}
                    voterPositionLevel={positionLevel.purok}
                    headId={id ?? 0}
                    onHasChange={hasChange}
                  />
                </div>
              )}
              {showLiner && (
                <div
                  className={
                    selectedTab === 'LINER' ? 'selected-content' : 'tab-content'
                  }>
                  <VoterMember
                    selectVoter={selectVoter}
                    voterPositionLevel={positionLevel.liner}
                    headId={id ?? 0}
                    onHasChange={hasChange}
                  />
                </div>
              )}
              {showCoding && (
                <div
                  className={
                    selectedTab === 'CODING'
                      ? 'selected-content'
                      : 'tab-content'
                  }>
                  <VoterMember
                    selectVoter={selectVoter}
                    voterPositionLevel={positionLevel.coding}
                    headId={id ?? 0}
                    onHasChange={hasChange}
                  />
                </div>
              )}
            </div>
          </div>
        )}
      </div>

      <div>
        {showModal && (
          <SearchVoterModal
            onVoterSelect={selectSearchVoter}
            onCloseModal={onClose}
          />
        )}
      </div>
      <div>
        {showManageModal && (
          <ManageVoter
            votersInfo={selectedVoter}
            onCloseModal={onManageClose}
          />
        )}
      </div>
    </div>
  );
}
