import { IconProp } from '@fortawesome/fontawesome-svg-core';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import { useSetBusy, useSetMessage } from '../custom-hooks/authorize-provider';
import { VoterData } from '../entities/voter/VoterData';
import { downloadFile } from '../helpers';
import { getBarangays } from '../processors/barangay';
import { getClusters } from '../processors/cluster';
import {
  getDailyDistribution,
  getDailyLeaderDistribution,
  getDistribution2Leaders,
  getDistribution2Receiving,
  getDistributionLeaderReceivingByDate,
  getDistributionReceivingByDate,
  getEday,
  getIndividualDistributionReceiving,
  getIndividualEday,
  getIndividualSampleBallot,
  getIndividualWCSampleBallot,
  getPasasalamat,
  getSampleBallot,
  getSampleBallotUnscanned,
} from '../processors/voter';
import CustomCheckBox from './components/custom-check-box';
import CustomDatePicker from './components/custom-datepicker';
import CustomDropdown, { DropdownItem } from './components/custom-dropdown';
import { CustomReturn } from './components/CustomReturn';
import SearchVoterModal from './modals/search-voter-modal';

export default function Downloads() {
  const [clusters, setClusters] = useState<DropdownItem[]>([]);
  const [barangays, setBarangays] = useState<DropdownItem[]>([]);
  const [selectedClusterDR, setSelectedClusterDR] = useState<string>('-1');
  const [selectedClusterDR2, setSelectedClusterDR2] = useState<string>('-1');
  const [selectedClusterSB, setSelectedClusterSB] = useState<string>('-1');
  const [selectedClusterE, setSelectedClusterE] = useState<string>('-1');
  const [selectedClusterP, setSelectedClusterP] = useState<string>('-1');
  const [selectedClusterDL, setSelectedClusterDL] = useState<string>('-1');
  const [selectedClusterSB2, setSelectedClusterSB2] = useState<string>('-1');
  const [selectedClusterLDR, setSelectedClusterLDR] = useState<string>('-1');
  const [selectedBarangayDR, setSelectedBarangayDR] = useState<string>('-1');
  const [selectedBarangayDR2, setSelectedBarangayDR2] = useState<string>('-1');
  const [selectedBarangaySB, setSelectedBarangaySB] = useState<string>('-1');
  const [selectedBarangaySB2, setSelectedBarangaySB2] = useState<string>('-1');
  const [selectedBarangayLDR, setSelectedBarangayLDR] = useState<string>('-1');
  const [selectedBarangayD, setSelectedBarangayD] = useState<string>('-1');
  const [selectedBarangayLD, setSelectedBarangayLD] = useState<string>('-1');
  const [distributionDate, setDistributionDate] = useState(() => new Date());
  const [distributionDateL, setDistributionDateL] = useState(() => new Date());
  const [distributionDateD, setDistributionDateD] = useState(() => new Date());
  const [distributionDateLD, setDistributionDateLD] = useState(
    () => new Date()
  );
  const [checkDist, setCheckDist] = useState(false);
  const [checkDist2, setCheckDist2] = useState(false);
  const [checkDistL, setCheckDistL] = useState(false);
  const [checkDistI, setCheckDistI] = useState(false);

  const [selectedCodingName, setSelectedCodingName] = useState<VoterData>();
  const [showCodingSearch, setShowCodingSearch] = useState(false);
  const [showEdaySearch, setShowEdaySearch] = useState(false);
  const [selectedVotersName, setSelectedVotersName] = useState<VoterData>();
  const [selectedVotersWCName, setSelectedVotersWCName] = useState<VoterData>();
  const [selectedEdayName, setSelectedEdayName] = useState<VoterData>();
  const [showBallotSearch, setShowBallotSearch] = useState(false);
  const [showBallotWCSearch, setShowBallotWCSearch] = useState(false);
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();

  async function fetchCluster() {
    await getClusters()
      .then((res) => {
        if (res !== undefined) {
          setClusters([
            {
              key: '-1',
              value: 'All',
            },
            ...res.map((b) => {
              return {
                key: b.id.toString(),
                value: b.description ?? '',
              };
            }),
          ]);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      });
  }

  async function fetchBarangay() {
    await getBarangays()
      .then((res) => {
        if (res !== undefined) {
          setBarangays([
            {
              key: '-1',
              value: 'All',
            },
            ...res.map((b) => {
              return {
                key: b.id.toString(),
                value: b.name ?? '',
              };
            }),
          ]);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      });
  }

  function onChangeBDR({ value }: CustomReturn) {
    setSelectedBarangayDR((v) => value);
  }
  function onChangeBDR2({ value }: CustomReturn) {
    setSelectedBarangayDR2((v) => value);
  }
  function onChangeBSB({ value }: CustomReturn) {
    setSelectedBarangaySB((v) => value);
  }
  function onChangeBSB2({ value }: CustomReturn) {
    setSelectedBarangaySB2((v) => value);
  }
  function onChangeBLDR({ value }: CustomReturn) {
    setSelectedBarangayLDR((v) => value);
  }
  function onChangeBD({ value }: CustomReturn) {
    setSelectedBarangayD((v) => value);
  }
  function onChangeBLD({ value }: CustomReturn) {
    setSelectedBarangayLD((v) => value);
  }
  function onChangeDR({ value }: CustomReturn) {
    setSelectedClusterDR((v) => value);
  }
  function onChangeDR2({ value }: CustomReturn) {
    setSelectedClusterDR2((v) => value);
  }
  function onChangeCSB({ value }: CustomReturn) {
    setSelectedClusterSB((v) => value);
  }
  function onChangeE({ value }: CustomReturn) {
    setSelectedClusterE((v) => value);
  }
  function onChangeP({ value }: CustomReturn) {
    setSelectedClusterP((v) => value);
  }
  function onChangeCDL({ value }: CustomReturn) {
    setSelectedClusterDL((v) => value);
  }
  function onChangeCSB2({ value }: CustomReturn) {
    setSelectedClusterSB2((v) => value);
  }
  function onChangeLDR({ value }: CustomReturn) {
    setSelectedClusterLDR((v) => value);
  }
  function onDistributionDateChange({ value }: CustomReturn) {
    setDistributionDate((v) => value);
  }
  function onDistributionDateChangeL({ value }: CustomReturn) {
    setDistributionDateL((v) => value);
  }
  function onDistributionDateChangeD({ value }: CustomReturn) {
    setDistributionDateD((v) => value);
  }
  function onDistributionDateChangeLD({ value }: CustomReturn) {
    setDistributionDateLD((v) => value);
  }
  function checkDistChange() {
    setCheckDist((v) => !v);
  }
  function checkDist2Change() {
    setCheckDist2((v) => !v);
  }

  function checkDistIChange() {
    setCheckDistI((v) => !v);
  }

  function checkDistLChange() {
    setCheckDistL((v) => !v);
  }
  async function downloadDistributionLeaders() {
    setBusy(true);
    await getDistribution2Leaders(selectedClusterDL)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'distribution2_leaders_summary.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadSampleBallot() {
    setBusy(true);
    await getSampleBallot(selectedClusterSB, selectedBarangaySB)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'sample_ballot.zip');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadSampleBallot2() {
    setBusy(true);
    await getSampleBallotUnscanned(selectedClusterSB2, selectedBarangaySB2)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'sample_ballot_unscanned.zip');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadIndividualSampleBallot() {
    setBusy(true);
    await getIndividualSampleBallot(selectedVotersName?.id ?? 0)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'individual_sample_ballot.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadIndividualWCSampleBallot() {
    setBusy(true);
    await getIndividualWCSampleBallot(selectedVotersWCName?.id ?? 0)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'individual_sample_ballot_with_coding.zip');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadIndividualEDay() {
    setBusy(true);
    await getIndividualEday(selectedEdayName?.id ?? 0)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'individual_eday.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadDistributionReceiving() {
    setBusy(true);
    await getDistributionReceivingByDate(
      selectedClusterDR,
      selectedBarangayDR,
      distributionDate,
      checkDist
    )
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'distribution_receiving.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadDistribution2Receiving() {
    setBusy(true);
    await getDistribution2Receiving(
      selectedClusterDR2,
      selectedBarangayDR2,
      checkDist2
    )
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'distribution2_receiving.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadDistributionLeaderReceiving() {
    setBusy(true);
    await getDistributionLeaderReceivingByDate(
      selectedClusterLDR,
      selectedBarangayLDR,
      distributionDateL,
      checkDistL
    )
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'leaders_distribution_receiving.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  async function downloadIndividualDistributionReceiving() {
    setBusy(true);
    await getIndividualDistributionReceiving(
      selectedCodingName?.id ?? 0,
      checkDistI
    )
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'individual_distribution_receiving.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  async function downloadDailyDistribution() {
    setBusy(true);
    await getDailyDistribution(selectedBarangayD, distributionDateD)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'daily_distribution.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadDailyLeaderDistribution() {
    setBusy(true);
    await getDailyLeaderDistribution(selectedBarangayLD, distributionDateLD)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'daily_leader_distribution.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function initializeComponents() {
    await fetchCluster();
    await fetchBarangay();
  }
  async function downloadEday() {
    setBusy(true);
    await getEday(selectedClusterE)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'eday.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function downloadPasasalamat() {
    setBusy(true);
    await getPasasalamat(selectedClusterP)
      .then((res) => {
        if (res !== undefined) {
          downloadFile(res, 'pasasalamat.xlsx');
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  useEffect(
    () => {
      initializeComponents();
    },
    // eslint-disable-next-line
    []
  );

  return (
    <div className='main-container downloads-main-container'>
      <div className='item-container  downloads-container'>
        <div className='download-header'>PASASALAMAT</div>
        <div className='download-options'>
          <CustomDropdown
            title='Cluster'
            name='ClusterId'
            value={selectedClusterP}
            onChange={onChangeP}
            itemsList={clusters}
          />
          <button onClick={downloadPasasalamat} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>EDAY</div>
        <div className='download-options'>
          <CustomDropdown
            title='Cluster'
            name='ClusterId'
            value={selectedClusterE}
            onChange={onChangeE}
            itemsList={clusters}
          />
          <button onClick={downloadEday} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>Individual EDAY</div>
        <div className='download-options'>
          <div className='search-container'>
            <input
              type='text'
              className='name'
              placeholder='Search...'
              value={
                selectedEdayName !== undefined
                  ? `${selectedEdayName?.position?.name} - ${selectedEdayName?.fullName}`
                  : ''
              }
              readOnly={true}
            />
            <FontAwesomeIcon
              className='search-icon'
              icon={faSearch as IconProp}
              onClick={function (): void {
                setShowEdaySearch(() => true);
              }}
            />
          </div>
          <button onClick={downloadIndividualEDay} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>
          Sample Ballot (Scanned in Distribution)
        </div>
        <div className='download-options'>
          <CustomDropdown
            title='Barangay'
            name='barangayId'
            value={selectedBarangaySB}
            onChange={onChangeBSB}
            itemsList={barangays}
          />
          <CustomDropdown
            title='Cluster'
            name='ClusterId'
            value={selectedClusterSB}
            onChange={onChangeCSB}
            itemsList={clusters}
          />
          <button onClick={downloadSampleBallot} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>
          Sample Ballot (Without Scan in Distribution)
        </div>
        <div className='download-options'>
          <CustomDropdown
            title='Barangay'
            name='barangayId'
            value={selectedBarangaySB2}
            onChange={onChangeBSB2}
            itemsList={barangays}
          />
          <CustomDropdown
            title='Cluster'
            name='ClusterId'
            value={selectedClusterSB2}
            onChange={onChangeCSB2}
            itemsList={clusters}
          />
          <button onClick={downloadSampleBallot2} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>
          Individual Sample Ballot (With or Without Scan in Distribution)
        </div>
        <div className='download-options'>
          <div className='search-container'>
            <input
              type='text'
              className='name'
              placeholder='Search...'
              value={
                selectedVotersName !== undefined
                  ? `${selectedVotersName?.position?.name} - ${selectedVotersName?.fullName}`
                  : ''
              }
              readOnly={true}
            />
            <FontAwesomeIcon
              className='search-icon'
              icon={faSearch as IconProp}
              onClick={function (): void {
                setShowBallotSearch(() => true);
              }}
            />
          </div>
          <button onClick={downloadIndividualSampleBallot} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>
          Individual Sample Ballot (Leader with Coding)
        </div>
        <div className='download-options'>
          <div className='search-container'>
            <input
              type='text'
              className='name'
              placeholder='Search...'
              value={
                selectedVotersWCName !== undefined
                  ? `${selectedVotersWCName?.position?.name} - ${selectedVotersWCName?.fullName}`
                  : ''
              }
              readOnly={true}
            />
            <FontAwesomeIcon
              className='search-icon'
              icon={faSearch as IconProp}
              onClick={function (): void {
                setShowBallotWCSearch(() => true);
              }}
            />
          </div>
          <button onClick={downloadIndividualWCSampleBallot} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>Distribution 2 Leaders Summary</div>
        <div className='download-options'>
          <CustomDropdown
            title='Cluster'
            name='ClusterId'
            value={selectedClusterDL}
            onChange={onChangeCDL}
            itemsList={clusters}
          />
          <button onClick={downloadDistributionLeaders} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>Distribution 2 Receiving</div>
        <div className='download-options'>
          <CustomCheckBox
            text='Include Printed'
            isCheck={checkDist2}
            id='checkDist'
            checkChange={checkDist2Change}
          />
          <CustomDropdown
            title='Barangay'
            name='barangayId'
            value={selectedBarangayDR2}
            onChange={onChangeBDR2}
            itemsList={barangays}
          />
          <CustomDropdown
            title='Cluster'
            name='ClusterId'
            value={selectedClusterDR2}
            onChange={onChangeDR2}
            itemsList={clusters}
          />
          <button onClick={downloadDistribution2Receiving} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>Distribution Receiving</div>
        <div className='download-options'>
          <CustomCheckBox
            text='Include Printed'
            isCheck={checkDist}
            id='checkDist'
            checkChange={checkDistChange}
          />
          <CustomDropdown
            title='Barangay'
            name='barangayId'
            value={selectedBarangayDR}
            onChange={onChangeBDR}
            itemsList={barangays}
          />
          <CustomDropdown
            title='Cluster'
            name='ClusterId'
            value={selectedClusterDR}
            onChange={onChangeDR}
            itemsList={clusters}
          />
          <CustomDatePicker
            title='Distribution Date'
            name='distribution_date'
            value={distributionDate}
            onChange={onDistributionDateChange}
          />
          <button onClick={downloadDistributionReceiving} className='btn'>
            Download
          </button>
        </div>
      </div>

      <div className='item-container  downloads-container'>
        <div className='download-header'>Leaders Distribution Receiving</div>
        <div className='download-options'>
          <CustomCheckBox
            text='Include Printed'
            isCheck={checkDistL}
            id='checkDistL'
            checkChange={checkDistLChange}
          />
          <CustomDropdown
            title='Barangay'
            name='barangayId'
            value={selectedBarangayLDR}
            onChange={onChangeBLDR}
            itemsList={barangays}
          />
          <CustomDropdown
            title='Cluster'
            name='ClusterId'
            value={selectedClusterLDR}
            onChange={onChangeLDR}
            itemsList={clusters}
          />
          <CustomDatePicker
            title='Distribution Date'
            name='distribution_date'
            value={distributionDateL}
            onChange={onDistributionDateChangeL}
          />
          <button onClick={downloadDistributionLeaderReceiving} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>Individual Distribution Receiving</div>
        <div className='download-options'>
          <CustomCheckBox
            text='Include Printed'
            isCheck={checkDistI}
            id='checkDistI'
            checkChange={checkDistIChange}
          />
          <div className='search-container'>
            <input
              type='text'
              className='name'
              placeholder='Search...'
              value={selectedCodingName?.fullName}
              readOnly={true}
            />
            <FontAwesomeIcon
              className='search-icon'
              icon={faSearch as IconProp}
              onClick={function (): void {
                setShowCodingSearch(() => true);
              }}
            />
          </div>
          <button
            onClick={downloadIndividualDistributionReceiving}
            className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>Daily Distribution</div>
        <div className='download-options'>
          <CustomDropdown
            title='Barangay'
            name='barangayId'
            value={selectedBarangayD}
            onChange={onChangeBD}
            itemsList={barangays}
          />
          <CustomDatePicker
            title='Distribution Date'
            name='distribution_date'
            value={distributionDateD}
            onChange={onDistributionDateChangeD}
          />
          <button onClick={downloadDailyDistribution} className='btn'>
            Download
          </button>
        </div>
      </div>
      <div className='item-container  downloads-container'>
        <div className='download-header'>Daily Leader Distribution</div>
        <div className='download-options'>
          <CustomDropdown
            title='Barangay'
            name='barangayId'
            value={selectedBarangayLD}
            onChange={onChangeBLD}
            itemsList={barangays}
          />
          <CustomDatePicker
            title='Distribution Date'
            name='distribution_date'
            value={distributionDateLD}
            onChange={onDistributionDateChangeLD}
          />
          <button onClick={downloadDailyLeaderDistribution} className='btn'>
            Download
          </button>
        </div>
      </div>
      {showEdaySearch && (
        <SearchVoterModal
          withPositionOnly={true}
          onVoterSelect={function (voter: VoterData): void {
            setSelectedEdayName(() => voter);
            setShowEdaySearch(() => false);
          }}
          onCloseModal={function (): void {
            setShowEdaySearch(() => false);
          }}
        />
      )}
      {showCodingSearch && (
        <SearchVoterModal
          withPositionOnly={true}
          onVoterSelect={function (voter: VoterData): void {
            setSelectedCodingName(() => voter);
            setShowCodingSearch(() => false);
          }}
          onCloseModal={function (): void {
            setShowCodingSearch(() => false);
          }}
        />
      )}

      {showBallotSearch && (
        <SearchVoterModal
          withPositionOnly={true}
          onVoterSelect={function (voter: VoterData): void {
            setSelectedVotersName(() => voter);
            setShowBallotSearch(() => false);
          }}
          onCloseModal={function (): void {
            setShowBallotSearch(() => false);
          }}
        />
      )}
      {showBallotWCSearch && (
        <SearchVoterModal
          withPositionOnly={true}
          onVoterSelect={function (voter: VoterData): void {
            setSelectedVotersWCName(() => voter);
            setShowBallotWCSearch(() => false);
          }}
          onCloseModal={function (): void {
            setShowBallotWCSearch(() => false);
          }}
        />
      )}
    </div>
  );
}
