import React, { useEffect, useState } from 'react';
import { positionLevel } from '../../constant';
import {
  useSetBusy,
  useSetMessage,
} from '../../custom-hooks/authorize-provider';
import { VoterData } from '../../entities/voter/VoterData';
import { getVotersMembers } from '../../processors/voter';
import Modal from './modal';
import VoterRemarks from '../components/voter-remarks';
import { printMemberReportSingle } from '../../pdf-report.ts/report';

export default function Showvoters({
  head,
  level,
  onCloseModal,
}: {
  head: VoterData | undefined;
  level: number;
  onCloseModal: () => void;
}) {
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();
  const [total, setTotal] = useState(0);
  const [voters, setVoters] = useState<VoterData[]>([]);
  const [position] = useState(() => {
    return (
      (level === positionLevel.bcc && 'BARANGAY COORDINATOR') ||
      (level === positionLevel.purok && 'PUROK LEADER') ||
      (level === positionLevel.liner && 'LINER') ||
      (level === positionLevel.coding && 'CODING') ||
      ''
    );
  });
  useEffect(
    () => {
      getvoter();
    },
    // eslint-disable-next-line
    []
  );

  async function getvoter() {
    setBusy(true);
    await getVotersMembers(level ?? -1, 1, head?.id, 10000000)
      .then((res) => {
        if (res !== undefined) {
          setVoters(res.voters);
          setTotal(res.total);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  async function print() {
    printMemberReportSingle(voters, level, head);
  }

  return (
    <Modal
      className='display-voter-modal'
      onClose={onCloseModal}
      title={`${head?.fullName}'s ${position}`}>
      <div className='item-container display-voter-body'>
        <table className='item-table'>
          <thead>
            <tr>
              <th>Name</th>
              <th>Barangay</th>
              <th>Precinct</th>
              <th>Remarks</th>
            </tr>
          </thead>
          <tbody>
            {voters.map((voter) => (
              <tr key={voter.id}>
                <td>{voter.fullName}</td>
                <td>{voter.barangay?.name}</td>
                <td>{voter.precinctNumber}</td>
                <td>
                  <VoterRemarks voter={voter} />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className='footer'>
        <div className='total'>Total Count : {total.toLocaleString()}</div>
        <button className='btn btn-margin-all' onClick={print}>
          Print
        </button>
      </div>
    </Modal>
  );
}
