import React from 'react';
import { BarangayVote } from '../../entities/voter/BarangayVote';

export default function Statistics({ barangay }: { barangay: BarangayVote }) {
  return (
    <div className='stats-container'>
      <div
        className={
          'bar ' + (barangay.totalVoted === barangay.totalVoters && 'full')
        }
        style={{
          width: `calc(${barangay.percentage}% - 0.4em)`,
        }}></div>
      <div className='data'>
        <div className='barangay'>{barangay.name}</div>
        <div className='stats'>
          <div>
            {barangay.totalVoted?.toLocaleString()}&nbsp;/&nbsp;
            {barangay.totalVoters?.toLocaleString()}
          </div>
          <div className='percentage'>
            ({Math.round(barangay.percentage ?? 0)}
            %)
          </div>
        </div>
      </div>
    </div>
  );
}
