import React, { useState } from 'react';
import {
  useSetBusy,
  useSetMessage,
} from '../../custom-hooks/authorize-provider';
import { VoterData } from '../../entities/voter/VoterData';
import { searchVoter } from '../../processors/voter';
import PositionLeader from '../components/position-leader';
import SeachBar from '../components/seachbar';
import Modal from './modal';

export default function SearchVoterModal({
  onVoterSelect,
  onCloseModal,
  withPositionOnly,
  withoutPositionOnly,
}: {
  onVoterSelect: (voter: VoterData) => void;
  onCloseModal: () => void;
  withPositionOnly?: boolean;
  withoutPositionOnly?: boolean;
}) {
  const [searchKey, setSearchKey] = useState<string | undefined>();
  const [voters, setVoters] = useState<VoterData[]>([]);
  const [voterPageCount, setVoterPageCount] = useState<number>(1);
  const [currentVoterPage, setCurrentVoterPage] = useState<number>(1);
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();

  async function searchVotersList(key: string | undefined, page: number) {
    setBusy(true);
    console.log(withoutPositionOnly);
    await searchVoter(key, page, 50, withPositionOnly, withoutPositionOnly)
      .then((res) => {
        if (res !== undefined) {
          setVoters((v) => {
            return [...v, ...res.voters];
          });
          setVoterPageCount(res.pageCount);
          setCurrentVoterPage(page);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function searchVoters(key: string) {
    setSearchKey(key);
    setVoters([]);
    searchVotersList(key, 1);
  }

  function loadMore() {
    searchVotersList(searchKey, currentVoterPage + 1);
  }

  return (
    <Modal className='search-modal' onClose={onCloseModal} title='Search Voter'>
      <SeachBar search={searchVoters} />
      <div className='item-container voters-container search-modal-body'>
        <table className='item-table'>
          <thead>
            <tr>
              <th>Name</th>
              <th>Position</th>
              <th>Barangay</th>
              <th>Precinct</th>
            </tr>
          </thead>
          <tbody>
            {voters.map((voter) => (
              <tr
                key={voter.id}
                id={'voter-' + voter.id}
                onDoubleClick={() => {
                  onVoterSelect(voter);
                }}>
                <td>{voter.fullName}</td>
                <td>
                  <PositionLeader
                    position={voter.position}
                    leader={
                      voter.position?.tag === 'PN'
                        ? voter.pnLeader
                        : voter.jtcLeader
                    }
                    doublePosition={true}
                  />
                </td>
                <td>{voter.barangay?.name}</td>
                <td>{voter.precinctNumber}</td>
              </tr>
            ))}
          </tbody>
          {currentVoterPage !== voterPageCount && voterPageCount > 0 && (
            <tfoot>
              <tr onClick={loadMore}>
                <td colSpan={4} className='load-more' align='center'>
                  Load More
                </td>
              </tr>
            </tfoot>
          )}
        </table>
      </div>
    </Modal>
  );
}
