import { BrowserRouter, Route, NavLink, Switch } from 'react-router-dom';
import {
  useAuthorize,
  useSetMessage,
  useUpdateAuthorize,
  useUserProfile,
} from '../custom-hooks/authorize-provider';
import LoginPage from './login-page';
import UserPage from './user-page';

import { Routes } from '../routes';
import DCCManagementPage from './dcc-management-page';
import GroupManagementPage from './group-management-page';
import { useMemo, useState } from 'react';
import ManageProfile from './modals/manage-profile';
import { ICON } from '../constant';
import QRCodeReaderPage from './qrcode-reader-page';
import VotingPage from './voting-page';
import VotingStatisticsPage from './voting-statistics-page';
import VoterReportPage from './voter-report-page';
import AttendanceReportPage from './attendance-report-page';
import RegisteredVoterReportPage from './registered-voter-report-page';
import DistributionPage from './distribution-page';
import LeaderDistributionPage from './leader-distribution-page';
import Downloads from './download';

export default function HomePage() {
  const [showProfile, setShowProfile] = useState(false);
  const authorize = useAuthorize();
  const profile = useUserProfile();
  const updateAuthorize = useUpdateAuthorize();
  const setMessage = useSetMessage();
  const menus: { head: string; navs: { route: string; name: string }[] }[] =
    useMemo(
      () => [
        {
          head: 'Managements',
          navs: [
            {
              route: Routes.Dcc,
              name: 'Distrinct Coordinators',
            },
            {
              route: Routes.Group,
              name: 'Group',
            },
          ],
        },
        {
          head: 'Reports',
          navs: [
            {
              route: Routes.VoterReport,
              name: 'Voters',
            },
            {
              route: Routes.AttendanceReport,
              name: 'Attendaces',
            },
            {
              route: Routes.RegisteredReport,
              name: 'Registered Voters',
            },
            {
              route: Routes.Downloads,
              name: 'Downloads',
            },
            // {
            //   route: Routes.Statistics,
            //   name: 'Vote Statistics',
            // },
          ],
        },
        {
          head: 'Scanners',
          navs: [
            {
              route: Routes.Scanner,
              name: 'Attendance',
            },
            {
              route: Routes.Distribution,
              name: 'Distribution',
            },
            {
              route: Routes.LeaderDistribution,
              name: 'Leader Distribution',
            },
            // {
            //   route: Routes.Voting,
            //   name: 'Voting',
            // },
          ],
        },
      ],
      []
    );
  function logoutUser() {
    setMessage({
      message: 'Continue to logout?',
      action: 'YESNO',
      onOk: () => {
        updateAuthorize(false);
        setShowProfile(false);
      },
    });
  }

  return (
    <>
      {authorize ? (
        <BrowserRouter>
          <header className='navbar'>
            <div className='container'>
              <NavLink to={Routes.Home} className='icon'>
                {ICON}
              </NavLink>
              <nav>
                <div>
                  <ul className='navigations'>
                    <li>
                      <button className='nav-menu'>Navigations</button>
                      <div className='menus'>
                        {menus.map((menu) => (
                          <div className='menu-items' key={menu.head}>
                            <div className='head'>{menu.head}</div>
                            <div className='navs'>
                              {menu.navs.map((nav) => (
                                <div key={nav.route}>
                                  <NavLink to={nav.route}>{nav.name}</NavLink>
                                </div>
                              ))}
                            </div>
                          </div>
                        ))}
                      </div>
                    </li>
                    {profile?.admin && (
                      <li>
                        <NavLink to={Routes.User}>Users</NavLink>
                      </li>
                    )}
                  </ul>
                </div>
                <ul className='user'>
                  <li>
                    <label
                      className='user-name'
                      onClick={() => setShowProfile(true)}>
                      {profile?.name ?? '------'}
                    </label>
                  </li>
                  <li>
                    <label onClick={logoutUser}>Logout</label>
                  </li>
                </ul>
              </nav>
            </div>
          </header>
          <Switch>
            <Route path={Routes.Dcc} exact component={DCCManagementPage} />
            <Route path={Routes.Group} exact component={GroupManagementPage} />
            <Route
              path={Routes.VoterReport}
              exact
              component={VoterReportPage}
            />
            <Route
              path={Routes.AttendanceReport}
              exact
              component={AttendanceReportPage}
            />
            <Route
              path={Routes.RegisteredReport}
              exact
              component={RegisteredVoterReportPage}
            />
            <Route path={Routes.User} exact component={UserPage} />
            <Route path={Routes.Scanner} exact component={QRCodeReaderPage} />
            <Route path={Routes.Voting} exact component={VotingPage} />
            <Route
              path={Routes.Statistics}
              exact
              component={VotingStatisticsPage}
            />
            <Route
              path={Routes.Distribution}
              exact
              component={DistributionPage}
            />
            <Route
              path={Routes.LeaderDistribution}
              exact
              component={LeaderDistributionPage}
            />
            <Route path={Routes.Downloads} exact component={Downloads} />
          </Switch>
          <div>
            {showProfile && (
              <ManageProfile onClose={() => setShowProfile(false)} />
            )}
          </div>
        </BrowserRouter>
      ) : (
        <LoginPage />
      )}
    </>
  );
}
