import React, { useRef } from 'react';
import Modal from './modal';
import SignatureCanvas from 'react-signature-canvas';

export default function CaptureVoterSignature({
  name,
  onSave,
  onClose,
}: {
  name: string | undefined;
  onSave: (image: string) => void;
  onClose: () => void;
}) {
  const signRef = useRef<any>(null);
  function save() {
    if (signRef.current) onSave(signRef.current?.toDataURL('image/png', 0.1));
  }
  function clear() {
    if (signRef.current) signRef.current?.clear();
  }
  return (
    <Modal onClose={onClose}>
      <div className='signature-container'>
        <SignatureCanvas
          penColor='black'
          ref={signRef}
          canvasProps={{ width: 800, height: 400, className: 'sigCanvas' }}
        />
        <label className='name'>{name}</label>
      </div>
      <div className='modal-footer center-footer'>
        <button className='btn' onClick={clear}>
          Clear
        </button>
        <button className='btn' onClick={save}>
          Save Signature
        </button>
      </div>
    </Modal>
  );
}
