import React, { useEffect, useMemo, useState } from 'react';
import {
  useSetBusy,
  useSetMessage,
} from '../../custom-hooks/authorize-provider';
import { VoterData } from '../../entities/voter/VoterData';
import { getVotersMembers } from '../../processors/voter';
import VoterMemberManagement from '../modals/voter-member-management';
import Pagination from './pagination';
import VoterRemarks from './voter-remarks';

export default function VoterMember({
  selectVoter,
  voterPositionLevel,
  onHasChange,
  headId,
}: {
  selectVoter: (votersId: number) => void;
  onHasChange: () => void;
  voterPositionLevel: number;
  headId: number;
}) {
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();

  const [voters, setVoters] = useState<VoterData[]>([]);
  const [pageCount, setPageCount] = useState<number>(1);
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [total, setTotal] = useState(0);
  const [showManagement, setShowManagement] = useState(false);
  const selectedTab = useMemo(() => {
    switch (voterPositionLevel) {
      case 2:
        return 'BARANGAY COORDINATOR';
      case 3:
        return 'PUROK LEADER';
      case 4:
        return 'LINER';
      case 5:
        return 'CODING';
      default:
        return '';
    }
  }, [voterPositionLevel]);

  useEffect(
    () => {
      getVoter(1);
      return () => {
        setVoters([]);
        setPageCount(0);
        setCurrentPage(1);
        setTotal(0);
      };
    },
    // eslint-disable-next-line
    [headId]
  );

  async function getVoter(page: number) {
    setBusy(true);
    await getVotersMembers(voterPositionLevel, page, headId)
      .then((res) => {
        if (res !== undefined) {
          setVoters(res.voters);
          setPageCount(res.pageCount);
          setCurrentPage(page);
          setTotal(res.total);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function goToPage(page: number) {
    getVoter(page);
  }
  function manageMembers() {
    setShowManagement(true);
  }
  function onCloseManagement(hasChange: boolean) {
    setShowManagement(false);
    if (hasChange) {
      getVoter(1);
      onHasChange();
    }
  }
  return (
    <div className='item-container voter-container'>
      <Pagination
        pages={pageCount}
        currentPageNumber={currentPage}
        goInPage={goToPage}></Pagination>
      <div className='content'>
        <table className='item-table'>
          <thead>
            <tr>
              <th>Name</th>
              <th>Barangay</th>
              <th>Precinct</th>
              <th>Remarks</th>
            </tr>
          </thead>
          <tbody>
            {voters.map((voter) => (
              <tr
                key={voter.id}
                id={'voter-' + voter.id}
                onDoubleClick={() => selectVoter(voter.id)}>
                <td>{voter.fullName}</td>
                <td>{voter.barangay?.name}</td>
                <td>{voter.precinctNumber}</td>
                <td>
                  <VoterRemarks voter={voter} />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className='footer'>
        <div className='total'>Total Count : {total.toLocaleString()}</div>
        <div className='actions'>
          <button className='btn' onClick={manageMembers}>
            Manage
          </button>
        </div>
      </div>

      <div>
        {showManagement && (
          <VoterMemberManagement
            onClose={onCloseManagement}
            headId={headId}
            voterPositionLevel={voterPositionLevel}
            position={selectedTab}
          />
        )}
      </div>
    </div>
  );
}
