import React, { useEffect, useState } from 'react';
import { positionLevel } from '../constant';
import { useSetBusy, useSetMessage } from '../custom-hooks/authorize-provider';
import { VoterData } from '../entities/voter/VoterData';
import { getBarangays } from '../processors/barangay';
import { getClusters } from '../processors/cluster';
import {
  getHierarchyReport,
  getVotersMembers,
  searchVoters,
} from '../processors/voter';
import { getVoterPositions } from '../processors/voter-position';
import CustomDropdown, { DropdownItem } from './components/custom-dropdown';
import CustomTextBox from './components/custom-textbox';
import { CustomReturn } from './components/CustomReturn';
import ShowMembers from './modals/show-member';
import {
  printBCCReport,
  printDistributionData,
  printInvitationReport,
  printMemberReport,
  printReport,
} from '../pdf-report.ts/report';
import CustomCheckBox from './components/custom-check-box';
import PositionLeader from './components/position-leader';
import {
  getDistributionTemplate,
  getInvitationConfig,
} from '../processors/configs';

interface Report {
  position?: string;
  district?: string;
  barangay?: string;
  precinct?: string;
  precinctStart?: string;
  precinctEnd?: string;
  name?: string;
  cluster?: string;

  positionToggle: boolean;
  districtToggle: boolean;
  barangayToggle: boolean;
  precinctToggle: boolean;
  precinctRangeToggle: boolean;
  nameToggle: boolean;
  clusterToggle: boolean;
  noBcc: boolean;
  noPurok: boolean;
  noLiner: boolean;
  noCoding: boolean;
}

export default function VoterReportPage() {
  const [barangays, setBarangays] = useState<DropdownItem[]>([]);
  const [districts, setDistricts] = useState<DropdownItem[]>([]);
  const [positions, setPositions] = useState<DropdownItem[]>([]);
  const [clusters, setClusters] = useState<DropdownItem[]>([]);
  const printSelection = [
    {
      key: 'Selected',
      value: 'Selected',
    },
    {
      key: 'Checked',
      value: 'Checked',
    },
    {
      key: 'All',
      value: 'All',
    },
    {
      key: 'Selected1',
      value: 'Selected With Coding',
    },
    {
      key: 'Checked1',
      value: 'Checked With Coding',
    },
    {
      key: 'All1',
      value: 'All With Coding',
    },
  ];
  const [selectedPrintSelection, setSelectedPrintSelection] = useState<
    string | undefined
  >();
  const [report, setReport] = useState<Report>({
    positionToggle: false,
    districtToggle: false,
    barangayToggle: false,
    precinctToggle: false,
    precinctRangeToggle: false,
    nameToggle: false,
    clusterToggle: false,
    noBcc: false,
    noPurok: false,
    noLiner: false,
    noCoding: false,
  });

  const [currentReport, setCurrentReport] = useState<Report>(report);
  const setMessage = useSetMessage();
  const setBusy = useSetBusy();
  const [total, setTotal] = useState(0);
  const [voters, setVoters] = useState<VoterData[]>([]);
  const [selectedVoter, setSelectedVoter] = useState<VoterData | undefined>();
  const [pageCount, setPageCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(0);
  const [checkAll, setCheckAll] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [level, setLevel] = useState<number | undefined>();
  useEffect(
    () => {
      initializeComponents();
    },
    // eslint-disable-next-line
    []
  );

  function onChange({ elementName, value }: CustomReturn) {
    setReport((r) => {
      return { ...r, [elementName]: value };
    });
  }
  function onToggle(elementName: string, value: boolean) {
    setReport((r) => {
      return { ...r, [elementName]: !value };
    });
  }
  async function initializeComponents() {
    await fetchBarangay();
    await fetchPosition();
    await fetchCluster();
    setTotal(0);
  }

  async function fetchBarangay() {
    await getBarangays()
      .then((res) => {
        if (res !== undefined) {
          setBarangays([
            {
              key: '',
              value: '',
            },
            ...res.map((b) => {
              return {
                key: b.id.toString(),
                value: b.name ?? '',
              };
            }),
          ]);
          setDistricts([
            {
              key: '',
              value: '',
            },
            ...res
              .map((b) => b.district)
              .filter((value, index, self) => self.indexOf(value) === index)
              .map((x) => {
                return {
                  key: x?.toString() ?? '',
                  value: x?.toString() ?? '',
                };
              }),
          ]);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      });
  }
  async function fetchPosition() {
    await getVoterPositions()
      .then((res) => {
        if (res !== undefined) {
          setPositions([
            {
              key: '',
              value: '',
            },
            ...res.map((b) => {
              return {
                key: b.id.toString(),
                value: b.name ?? '',
              };
            }),
            {
              key: '50',
              value: 'PENDING',
            },
            {
              key: '1000',
              value: 'HIERARCHY',
            },
          ]);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      });
  }
  async function fetchCluster() {
    await getClusters()
      .then((res) => {
        if (res !== undefined) {
          setClusters([
            {
              key: '',
              value: '',
            },
            ...res.map((b) => {
              return {
                key: b.id.toString(),
                value: b.description ?? '',
              };
            }),
          ]);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      });
  }
  async function fetchVoters(page: number, report: Report) {
    setBusy(true);
    if (page === 1) setVoters([]);
    await searchVoters(
      report.nameToggle ? report.name : undefined,
      report.positionToggle ? report.position : undefined,
      report.districtToggle ? report.district : undefined,
      report.barangayToggle ? report.barangay : undefined,
      report.precinctToggle ? report.precinct : undefined,
      report.precinctRangeToggle ? report.precinctStart : undefined,
      report.precinctRangeToggle ? report.precinctEnd : undefined,
      report.clusterToggle ? report.cluster : undefined,
      report.noBcc,
      report.noPurok,
      report.noLiner,
      report.noCoding,
      page,
      50
    )
      .then((res) => {
        if (res !== undefined) {
          setVoters((r) => [...r, ...res.voters]);
          setCurrentPage(page);
          setPageCount(res.pageCount);
          setTotal(res.total);
          setCurrentReport(report);
          setSelectedVoter(undefined);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .then(() => {
        setBusy(false);
      });
  }
  function search() {
    fetchVoters(1, report);
  }
  function loadMoreVoters() {
    fetchVoters(currentPage + 1, currentReport);
  }
  function selectVoter(votersData: VoterData) {
    setVoters((voter) =>
      voter.map((x) => {
        x.selected = x.id === votersData.id;
        return x;
      })
    );
    setSelectedVoter(votersData);
  }

  function checkVoter(votersData: VoterData) {
    setVoters(
      voters.map((x) => {
        if (x.id === votersData.id) {
          x.checked = !x.checked;
        }
        return x;
      })
    );
  }
  function checkAllVoter() {
    setCheckAll((res) => {
      res = !res;
      setVoters(
        voters.map((x) => {
          x.checked = res;
          return x;
        })
      );
      return res;
    });
  }
  function showMemberModal(level: number) {
    setLevel(level);
    setShowModal(true);
  }
  async function printBcc() {
    let voterToPrint: VoterData[] = [];
    switch (selectedPrintSelection) {
      case 'Checked':
        voterToPrint = voters.filter(
          (x) =>
            x.checked &&
            (x.position?.level === positionLevel.bcc ||
              x.position2?.level === positionLevel.bcc)
        );
        break;
      case 'Selected':
      default:
        voterToPrint = voters.filter(
          (x) =>
            x.selected &&
            (x.position?.level === positionLevel.bcc ||
              x.position2?.level === positionLevel.bcc)
        );
        break;
    }
    setBusy(true);
    await printBCCReport(voterToPrint)
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => {
        setBusy(false);
      });
  }

  async function printMember(headLevel: number, memberLevel: number) {
    let voterToPrint: VoterData[] = [];
    switch (selectedPrintSelection) {
      case 'Checked':
        voterToPrint = voters.filter(
          (x) =>
            x.checked &&
            (x.position?.level === headLevel ||
              x.position2?.level === headLevel)
        );
        break;
      case 'Selected':
      default:
        voterToPrint = voters.filter(
          (x) =>
            x.selected &&
            (x.position?.level === headLevel ||
              x.position2?.level === headLevel)
        );
        break;
    }
    setBusy(true);
    await printMemberReport(voterToPrint, headLevel, memberLevel)
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => {
        setBusy(false);
      });
  }

  async function getData(level: number, headId: number): Promise<VoterData[]> {
    return (
      (await getVotersMembers(level, 1, headId, 10000000)
        .then((res) => {
          if (res !== undefined) {
            return res.voters;
          }
          return [];
        })
        .catch((err) => {
          throw err;
        })) ?? []
    );
  }
  async function printHierarchy() {
    setBusy(true);
    await getHierarchyReport(report.cluster ?? '')
      .then((res) => {
        if (res !== undefined) {
          let link = document.createElement('a');
          link.href = res;
          link.setAttribute('download', 'hierarchy.xlsx');
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function fetchAllVoters(): Promise<VoterData[] | void> {
    setBusy(true);
    return await searchVoters(
      report.nameToggle ? report.name : undefined,
      report.positionToggle ? report.position : undefined,
      report.districtToggle ? report.district : undefined,
      report.barangayToggle ? report.barangay : undefined,
      report.precinctToggle ? report.precinct : undefined,
      report.precinctRangeToggle ? report.precinctStart : undefined,
      report.precinctRangeToggle ? report.precinctEnd : undefined,
      report.clusterToggle ? report.cluster : undefined,
      report.noBcc,
      report.noPurok,
      report.noLiner,
      report.noCoding,
      1,
      total
    )
      .then((res) => {
        setBusy(false);
        return res?.voters;
      })
      .finally(() => {
        setBusy(false);
      });
  }
  async function printDistribution() {
    let voterToPrint: VoterData[] = [];
    switch (selectedPrintSelection) {
      case 'All':
      case 'All1':
        voterToPrint =
          report.position === '1000' || currentPage === pageCount
            ? voters
            : (await fetchAllVoters()) ?? [];
        break;
      case 'Checked':
      case 'Checked1':
        voterToPrint = voters.filter((x) => x.checked);
        break;
      case 'Selected':
      case 'Selected1':
      default:
        voterToPrint = voters.filter((x) => x.selected);
        break;
    }
    setBusy(true);
    const template = await getDistributionTemplate();
    let votersToPrint = voterToPrint;
    if (
      selectedPrintSelection?.indexOf('1') !== undefined &&
      selectedPrintSelection?.indexOf('1') !== -1
    ) {
      votersToPrint = [];
      for (var x = 0; x < voterToPrint.length; x++) {
        votersToPrint.push(voterToPrint[x]);
        votersToPrint.push(
          ...(await getData(positionLevel.coding, voterToPrint[x].id))
        );
      }
    }
    let fileCnt = 1;
    let vt: VoterData[] = [];
    for (var i: number = 0; i < votersToPrint.length; i++) {
      vt.push(votersToPrint[i]);
      if ((i + 1) % 100 === 0) {
        await printDistributionData(vt, template, fileCnt);
        vt = [];
        fileCnt++;
      }
    }
    if (vt.length > 0) {
      await printDistributionData(vt, template, fileCnt);
      vt = [];
    }
    setBusy(false);
  }
  async function downloadLinersInvitation() {
    const config = await getInvitationConfig();
    let voterToPrint: VoterData[] = [];
    switch (selectedPrintSelection) {
      case 'All':
        voterToPrint =
          report.position === '1000' || currentPage === pageCount
            ? voters
            : (await fetchAllVoters()) ?? [];
        break;
      case 'Checked':
        voterToPrint = voters.filter((x) => x.checked);
        break;
      case 'Selected':
      default:
        voterToPrint = voters.filter((x) => x.selected);
        break;
    }
    setBusy(true);
    try {
      let vts: VoterData[] = [];
      for (let i: number = 0; i < voterToPrint.length; i++) {
        vts.push(voterToPrint[i]);
        vts.push(...(await getData(positionLevel.liner, voterToPrint[i].id)));
      }
      let vt: VoterData[] = [];
      for (let i: number = 0; i < vts.length; i++) {
        vt.push(vts[i]);
        if ((i + 1) % 100 === 0) {
          await printInvitationReport(vt, config);
          vt = [];
        }
      }
      if (vt.length > 0) {
        await printInvitationReport(vt, config);
      }
    } finally {
      setBusy(false);
    }
  }
  async function printInvitations() {
    const config = await getInvitationConfig();
    let voterToPrint: VoterData[] = [];
    switch (selectedPrintSelection) {
      case 'All':
        voterToPrint =
          report.position === '1000' || currentPage === pageCount
            ? voters
            : (await fetchAllVoters()) ?? [];
        break;
      case 'Checked':
        voterToPrint = voters.filter((x) => x.checked);
        break;
      case 'Selected':
      default:
        voterToPrint = voters.filter((x) => x.selected);
        break;
    }
    setBusy(true);
    try {
      let vt: VoterData[] = [];
      for (var i: number = 0; i < voterToPrint.length; i++) {
        vt.push(voterToPrint[i]);
        if ((i + 1) % 100 === 0) {
          await printInvitationReport(vt, config);
          vt = [];
        }
      }
      if (vt.length > 0) {
        await printInvitationReport(vt, config);
      }
    } finally {
      setBusy(false);
    }
  }

  async function print() {
    let voterToPrint: VoterData[] = [];
    switch (selectedPrintSelection) {
      case 'All':
        voterToPrint =
          report.position === '1000' || currentPage === pageCount
            ? voters
            : (await fetchAllVoters()) ?? [];
        break;
      case 'Checked':
        voterToPrint = voters.filter((x) => x.checked);
        break;
      case 'Selected':
      default:
        voterToPrint = voters.filter((x) => x.selected);
        break;
    }
    printReport(voterToPrint);
  }
  return (
    <div className='main-container'>
      <div className='item-container no-padding-top-container'>
        <div className='report-filter'>
          <div
            className={
              'checked-control-container ' +
              (report.positionToggle && ' checked')
            }
            onDoubleClick={() => {
              onToggle('positionToggle', report.positionToggle);
            }}>
            <CustomDropdown
              title='Position'
              itemsList={positions}
              name='position'
              value={report.position}
              onChange={onChange}
            />
          </div>
          <div
            className={
              'checked-control-container ' +
              (report.districtToggle && ' checked')
            }
            onDoubleClick={() => {
              onToggle('districtToggle', report.districtToggle);
            }}>
            <CustomDropdown
              title='District'
              itemsList={districts}
              onChange={onChange}
              name='district'
              value={report.district}
            />
          </div>
          <div
            className={
              'checked-control-container ' +
              (report.barangayToggle && ' checked')
            }
            onDoubleClick={() => {
              onToggle('barangayToggle', report.barangayToggle);
            }}>
            <CustomDropdown
              title='Barangay'
              itemsList={barangays}
              onChange={onChange}
              name='barangay'
              value={report.barangay}
            />
          </div>
          <div
            className={
              'checked-control-container ' +
              (report.precinctToggle && ' checked')
            }
            onDoubleClick={() => {
              onToggle('precinctToggle', report.precinctToggle);
            }}>
            <CustomTextBox
              title='Precinct Number'
              name='precinct'
              value={report.precinct}
              onChange={onChange}
            />
          </div>
          <div
            className={
              'checked-control-container multi-control ' +
              (report.precinctRangeToggle && ' checked')
            }
            onDoubleClick={() => {
              onToggle('precinctRangeToggle', report.precinctRangeToggle);
            }}>
            <CustomTextBox
              title='Precinct Number Start'
              name='precinctStart'
              value={report.precinctStart}
              onChange={onChange}
            />
            <span>-</span>
            <CustomTextBox
              title='Precinct Number End'
              name='precinctEnd'
              value={report.precinctEnd}
              onChange={onChange}
            />
          </div>
          <div
            className={
              'checked-control-container ' + (report.nameToggle && ' checked')
            }
            onDoubleClick={() => {
              onToggle('nameToggle', report.nameToggle);
            }}>
            <CustomTextBox
              title='Name'
              name='name'
              value={report.name}
              onChange={onChange}
            />
          </div>
          <div
            className={
              'checked-control-container ' +
              (report.clusterToggle && ' checked')
            }
            onDoubleClick={() => {
              onToggle('clusterToggle', report.clusterToggle);
            }}>
            <CustomDropdown
              title='Cluster'
              name='cluster'
              value={report.cluster}
              itemsList={clusters}
              onChange={onChange}
            />
          </div>
          <div className='btn-container'>
            {['1', '2', '3', '4'].filter((x) => x === report.position).length >
              0 && (
              <div className='checkers'>
                {report.position === '1' && (
                  <CustomCheckBox
                    checkChange={() => {
                      onToggle('noBcc', report.noBcc);
                    }}
                    isCheck={report.noBcc}
                    text='No BCC'
                    id='bcc'
                  />
                )}
                {report.position === '2' && (
                  <CustomCheckBox
                    checkChange={() => {
                      onToggle('noPurok', report.noPurok);
                    }}
                    isCheck={report.noPurok}
                    text='No Purok'
                    id='purok'
                  />
                )}
                {report.position === '3' && (
                  <CustomCheckBox
                    checkChange={() => {
                      onToggle('noLiner', report.noLiner);
                    }}
                    isCheck={report.noLiner}
                    text='No Liner'
                    id='liner'
                  />
                )}
                <CustomCheckBox
                  checkChange={() => {
                    onToggle('noCoding', report.noCoding);
                  }}
                  isCheck={report.noCoding}
                  text='No Coding'
                  id='coding'
                />
              </div>
            )}
            <button onClick={search} className='btn'>
              Search
            </button>
          </div>
        </div>
      </div>
      <div className='item-container report-content'>
        <table className='report-table item-table'>
          <thead>
            <tr>
              <th>
                <CustomCheckBox
                  checkChange={checkAllVoter}
                  isCheck={checkAll}
                />
              </th>
              <th></th>
              <th>Full Name</th>
              <th>Position</th>
              <th>District</th>
              <th>Barangay</th>
              <th>Cluster</th>
              <th>Precinct Number</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {voters.map((voter) => (
              <tr
                key={voter.id}
                onClick={() => selectVoter(voter)}
                className={voter.selected ?? false ? 'selected' : ''}>
                <td>
                  <CustomCheckBox
                    checkChange={() => checkVoter(voter)}
                    isCheck={voter.checked ?? false}
                  />
                </td>
                <td>
                  <img
                    src={voter?.picture}
                    height={35}
                    width={35}
                    alt='Voter'
                  />
                </td>
                <td>{voter.fullName}</td>
                <td>
                  <PositionLeader
                    position={voter.position}
                    leader={
                      voter.position?.tag === 'PN'
                        ? voter.pnLeader
                        : voter.jtcLeader
                    }
                    doublePosition={voter.position2?.name !== null}
                  />
                  <PositionLeader
                    position={voter.position2}
                    leader={
                      voter.position2?.tag === 'PN'
                        ? voter.pnLeader
                        : voter.jtcLeader
                    }
                    doublePosition={true}
                    pending={
                      voter.position2?.level === 5 ||
                      voter.position2?.level === 10
                    }
                  />
                </td>
                <td>{voter.barangay?.district}</td>
                <td>{voter.barangay?.name}</td>
                <td>{voter.cluster}</td>
                <td>{voter.precinctNumber}</td>
                <td>
                  <div className='voter-count-container'>
                    {voter.bccCount !== null && (
                      <div className='voter-count'>{`BCC Count: ${voter.bccCount}`}</div>
                    )}
                    {voter.purokCount !== null && (
                      <div className='voter-count'>{`Purok Count: ${voter.purokCount}`}</div>
                    )}
                    {voter.linerCount !== null && (
                      <div className='voter-count'>{`Liner Count: ${voter.linerCount}`}</div>
                    )}
                    {voter.codingCount !== null && (
                      <div className='voter-count'>{`Coding Count: ${voter.codingCount}`}</div>
                    )}
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
          {currentPage !== pageCount && pageCount > 0 && (
            <tfoot>
              <tr onClick={loadMoreVoters}>
                <td colSpan={8} className='load-more' align='center'>
                  Load More
                </td>
              </tr>
            </tfoot>
          )}
        </table>
      </div>
      <div className='item-container report-footer'>
        <div className='count'>Total: {total.toLocaleString()}</div>
        <div className='member-actions'>
          <div>
            <CustomDropdown
              title='Print Selection'
              value={selectedPrintSelection}
              itemsList={printSelection}
              className='print-selection'
              onChange={(data) => {
                setSelectedPrintSelection(data.value);
              }}
            />
          </div>
          <button className='btn' onClick={print}>
            Print
          </button>
          {report.position === '1000' && (
            <button className='btn' onClick={printHierarchy}>
              Print Heirarchy
            </button>
          )}
          <button className='btn' onClick={printDistribution}>
            Print Distribution
          </button>
          <button className='btn' onClick={printInvitations}>
            Print Invitations
          </button>
          {((selectedVoter?.position?.tag === 'PN' &&
            selectedVoter.position?.level === positionLevel.dcc) ||
            (selectedVoter?.position2?.tag === 'PN' &&
              selectedVoter.position2?.level === positionLevel.dcc)) && (
            <>
              <button
                className='btn'
                onClick={() => showMemberModal(positionLevel.bcc)}>
                View Barangay Coordinators
              </button>
              <button
                className='btn'
                onClick={() =>
                  printMember(positionLevel.dcc, positionLevel.bcc)
                }>
                Print Barangay Coordinators
              </button>
            </>
          )}
          {((selectedVoter?.position?.tag === 'PN' &&
            selectedVoter.position?.level === positionLevel.bcc) ||
            (selectedVoter?.position2?.tag === 'PN' &&
              selectedVoter.position2?.level === positionLevel.bcc)) && (
            <>
              <button className='btn' onClick={printBcc}>
                Print Group
              </button>
              <button
                className='btn'
                onClick={() => showMemberModal(positionLevel.purok)}>
                View Purok Leaders
              </button>
              <button
                className='btn'
                onClick={() =>
                  printMember(positionLevel.bcc, positionLevel.purok)
                }>
                Print Purok Leaders
              </button>
            </>
          )}
          {((selectedVoter?.position?.tag === 'PN' &&
            selectedVoter.position?.level === positionLevel.purok) ||
            (selectedVoter?.position2?.tag === 'PN' &&
              selectedVoter.position2?.level === positionLevel.purok)) && (
            <>
              <button className='btn' onClick={downloadLinersInvitation}>
                Print Liners Invitation
              </button>
              <button
                className='btn'
                onClick={() => showMemberModal(positionLevel.liner)}>
                View Liners
              </button>
              <button
                className='btn'
                onClick={() =>
                  printMember(positionLevel.purok, positionLevel.liner)
                }>
                Print Liners
              </button>
            </>
          )}

          {((selectedVoter?.position?.tag === 'PN' &&
            selectedVoter.position?.level !== positionLevel.coding) ||
            (selectedVoter?.position2?.tag === 'PN' &&
              selectedVoter.position2?.level !== positionLevel.coding)) && (
            <>
              <button
                className='btn'
                onClick={() => showMemberModal(positionLevel.coding)}>
                View Codings
              </button>
              <button
                className='btn'
                onClick={() =>
                  printMember(
                    selectedVoter?.position?.level ?? 0,
                    positionLevel.coding
                  )
                }>
                Print Codings
              </button>
            </>
          )}
        </div>
      </div>

      <div>
        {showModal && (
          <ShowMembers
            head={selectedVoter}
            level={level ?? 0}
            onCloseModal={() => {
              setShowModal(false);
            }}
          />
        )}
      </div>
    </div>
  );
}
