import React, { useEffect, useRef, useState } from 'react';
import { useSetBusy, useSetMessage } from '../custom-hooks/authorize-provider';
import {
  logLeaderDistribution,
  updateVoterPicture,
  updateVoterSignature,
} from '../processors/voter';
import PositionLeader from './components/position-leader';
import CaptureVoterImage from './modals/capture-voter-image';
import default_photo from '../default_photo.png';
import { faCamera, faPen } from '@fortawesome/free-solid-svg-icons';
import { IconProp } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { APP_KEY } from '../constant';
import { LeaderDistribution } from '../entities/voter/LeaderDistribution';

export default function LeaderDistributionPage() {
  const [value, setValue] = useState('');
  const txtRef = useRef<any>();
  const [typing, setTyping] = useState(false);
  const setBusy = useSetBusy();
  const [isInvalid, setIsInvalid] = useState(false);
  const [distribution, setDistribution] = useState<
    LeaderDistribution | undefined
  >();
  const [showCamera, setShowCamera] = useState(false);
  const [showSignature, setShowSignature] = useState(false);
  const setMessage = useSetMessage();

  useEffect(
    () => {
      const delayDebounceFn = setTimeout(() => {
        setTyping(false);
        getVotersData(value);
      }, 100);

      return () => clearTimeout(delayDebounceFn);
    },
    // eslint-disable-next-line
    [value]
  );

  async function getVotersData(qr: string) {
    if (!qr) return;
    setIsInvalid(false);
    setBusy(true);
    await logLeaderDistribution(qr)
      .then((res) => {
        if (res !== undefined) {
          setDistribution(res);
        }
      })
      .catch((err) => {
        setDistribution(undefined);
        setMessage({
          message: err.message,
          onOk: focusOnScanner,
        });
      })
      .finally(() => {
        setBusy(false);
      });
  }

  function focusOnScanner() {
    const delayDebounceFn = setTimeout(() => {
      if (txtRef.current) txtRef.current.focus();
      clearTimeout(delayDebounceFn);
    }, 100);
  }

  async function saveImage(image: string) {
    setBusy(true);
    await updateVoterPicture(image, distribution?.voterId ?? 0)
      .then((ret) => {
        if (ret) {
          setDistribution((vt) => {
            if (vt !== undefined && vt.voter !== undefined) {
              vt.voter.picture = image;
            }
            return vt;
          });
          cancelImageCapture();
        }
      })
      .catch((err) => {
        setMessage({
          message: err.message,
          onOk: focusOnScanner,
        });
      })
      .finally(() => setBusy(false));
  }
  function cancelImageCapture() {
    setShowCamera(() => false);
    focusOnScanner();
  }
  function openSignature() {
    setShowSignature(() => true);
  }
  function openCamera() {
    setShowCamera(() => true);
  }
  async function saveSignature(sign: string) {
    setBusy(true);
    await updateVoterSignature(sign, distribution?.voterId ?? 0)
      .then((ret) => {
        if (ret) {
          setDistribution((vt) => {
            if (vt !== undefined && vt.voter !== undefined) {
              vt.voter.signature = sign;
            }
            return vt;
          });
          cancelSignatureCapture();
        }
      })
      .catch((err) => {
        setMessage({
          message: err.message,
          onOk: focusOnScanner,
        });
      })
      .finally(() => setBusy(false));
  }
  function cancelSignatureCapture() {
    setShowSignature(() => false);
    focusOnScanner();
  }
  return (
    <>
      {showSignature && (
        <CaptureVoterImage
          onSave={saveSignature}
          onClose={cancelSignatureCapture}
          isPng={true}
        />
      )}
      {showCamera && (
        <CaptureVoterImage onSave={saveImage} onClose={cancelImageCapture} />
      )}
      <div
        id='scanner-main-container'
        className='scanner-main-container'
        onClick={focusOnScanner}>
        <div className='scanner-container'>
          <input
            className='hidden-scanner-input'
            type='text'
            value={value}
            ref={txtRef}
            autoFocus={true}
            onChange={(e) => {
              if (!typing) {
                if (txtRef.current)
                  txtRef.current.value =
                    txtRef.current.value[txtRef.current.value.length - 1];
                setTyping(true);
              }
              setValue(e.target.value);
            }}
          />
          <div className='scanner-content'>
            <div className='scanner-header'>
              Scan Leaders Distribution QRCode
            </div>
            <div className='center-photo'>
              <img
                src={distribution?.voter?.picture ?? default_photo}
                height={200}
                width={200}
                alt='Voter'
              />
              {distribution?.voter && (
                <>
                  <div className='icon-blocker'></div>
                  <div className='camera-icon'>
                    <FontAwesomeIcon
                      icon={faCamera as IconProp}
                      onClick={openCamera}
                    />
                  </div>
                </>
              )}
            </div>
            <div className='scanner-voters-info'>
              {isInvalid && <div>Invalid QRCode</div>}
              {distribution?.voter && (
                <>
                  <div className='log'>{distribution.status}</div>
                  <div className='center-photo'>
                    <img
                      src={distribution?.voter?.signature}
                      height={70}
                      width={200}
                      alt='Signature'
                    />
                    <div className='icon-blocker'></div>
                    <div className='signature-icon'>
                      <FontAwesomeIcon
                        icon={faPen as IconProp}
                        onClick={openSignature}
                      />
                    </div>
                  </div>
                  <div className='scanner-title'>Full Name</div>
                  <div className='name'>{distribution?.voter?.fullName}</div>
                  <div className='scanner-title'>Precinct Number</div>
                  <div>{distribution?.voter?.precinctNumber}</div>
                  <div className='scanner-title'>Barangay</div>
                  <div>{distribution?.voter?.barangay?.name}</div>
                  <div className='scanner-title'>Position</div>
                  <div>
                    <PositionLeader
                      position={distribution?.voter?.position}
                      leader={
                        distribution?.voter?.position?.tag === APP_KEY
                          ? distribution?.voter?.pnLeader
                          : distribution?.voter?.jtcLeader
                      }
                      doublePosition={
                        distribution?.voter?.position2?.name !== null
                      }
                    />
                    <PositionLeader
                      position={distribution?.voter?.position2}
                      leader={
                        distribution?.voter?.position2?.tag === APP_KEY
                          ? distribution?.voter?.pnLeader
                          : distribution?.voter?.jtcLeader
                      }
                      doublePosition={true}
                      pending={distribution?.voter?.position2?.level === 5}
                    />
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
