import React, { useEffect, useRef, useState } from 'react';
import { useSetBusy, useSetMessage } from '../custom-hooks/authorize-provider';
import default_photo from '../default_photo.png';
import { VoterData } from '../entities/voter/VoterData';
import { vote } from '../processors/voter';
import PositionLeader from './components/position-leader';

export default function VotingPage() {
  const [value, setValue] = useState('');
  const txtRef = useRef<any>();
  const [typing, setTyping] = useState(false);
  const setBusy = useSetBusy();
  const [isInvalid, setIsInvalid] = useState(false);
  const [voter, setVoter] = useState<VoterData | undefined>();
  const setMessage = useSetMessage();

  useEffect(
    () => {
      const delayDebounceFn = setTimeout(() => {
        setTyping(false);
        voteVoter(value);
      }, 100);

      return () => clearTimeout(delayDebounceFn);
    },
    // eslint-disable-next-line
    [value]
  );

  async function voteVoter(qr: string) {
    if (!qr) return;
    setIsInvalid(false);
    setBusy(true);
    await vote(qr)
      .then((res) => {
        if (res !== undefined) {
          setVoter(res);
        }
      })
      .catch((err) => {
        setVoter(undefined);
        setMessage({
          message: err.message,
          onOk: focusOnScanner,
        });
      })
      .finally(() => {
        setBusy(false);
      });
  }

  function focusOnScanner() {
    const delayDebounceFn = setTimeout(() => {
      if (txtRef.current) txtRef.current.focus();
      clearTimeout(delayDebounceFn);
    }, 100);
  }
  return (
    <>
      <div
        id='scanner-main-container'
        className='scanner-main-container'
        onClick={focusOnScanner}>
        <div className='scanner-container'>
          <input
            className='hidden-scanner-input'
            type='text'
            value={value}
            ref={txtRef}
            autoFocus={true}
            onChange={(e) => {
              if (!typing) {
                if (txtRef.current)
                  txtRef.current.value =
                    txtRef.current.value[txtRef.current.value.length - 1];
                setTyping(true);
              }
              setValue(e.target.value);
            }}
          />
          <div className='scanner-content'>
            <div className='scanner-header'>Scan QRCode</div>
            <div className='center-photo'>
              <img
                src={voter?.picture ?? default_photo}
                height={200}
                width={200}
                alt='Voter'
              />
            </div>
            <div className='scanner-voters-info'>
              {isInvalid && <div>Invalid QRCode</div>}
              {voter && (
                <>
                  <div className='log'>{voter.votingStatus}</div>
                  <div className='center-photo'>
                    <img
                      src={voter?.signature}
                      height={70}
                      width={200}
                      alt='Signature'
                    />
                  </div>
                  <div className='scanner-title'>Full Name</div>
                  <div className='name'>{voter?.fullName}</div>
                  <div className='scanner-title'>Precinct Number</div>
                  <div>{voter?.precinctNumber}</div>
                  <div className='scanner-title'>Barangay</div>
                  <div>{voter?.barangay?.name}</div>
                  <div className='scanner-title'>Position</div>
                  <div>
                    <PositionLeader
                      position={voter?.position}
                      leader={
                        voter?.position?.tag === 'JTC'
                          ? voter?.jtcLeader
                          : voter?.pnLeader
                      }
                      doublePosition={voter?.position2?.name !== null}
                    />
                    <PositionLeader
                      position={voter?.position2}
                      leader={
                        voter?.position2?.tag === 'JTC'
                          ? voter?.jtcLeader
                          : voter?.pnLeader
                      }
                      doublePosition={true}
                      pending={voter?.position2?.level === 5}
                    />
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
