import { IconProp } from '@fortawesome/fontawesome-svg-core';
import { faPlus, faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useEffect, useReducer, useState } from 'react';
import { positionLevel } from '../constant';
import {
  useSetBusy,
  useSetCurrentVoterId,
  useSetMessage,
} from '../custom-hooks/authorize-provider';
import { VoterData } from '../entities/voter/VoterData';
import { VoterPosition } from '../entities/voter/VoterPosition';
import {
  deleteVoterInTree,
  getVoterByLevel,
  insertVoterInTree,
  searchForMembers,
} from '../processors/voter';
import { getVoterPositions } from '../processors/voter-position';
import { Routes } from '../routes';
import PositionLeader from './components/position-leader';
import SeachBar from './components/seachbar';
import VoterRemarks from './components/voter-remarks';

type VoterAction =
  | {
      type: 'Delete';
      payload: number;
    }
  | {
      type: 'Fill';
      payload: VoterData[] | undefined;
    }
  | {
      type: 'Clear';
    };

type HeadAction =
  | {
      type: 'Add';
      payload: VoterData;
    }
  | {
      type: 'Delete';
      payload: number;
    }
  | {
      type: 'Fill';
      payload: VoterData[] | undefined;
    }
  | {
      type: 'Clear';
    };

function memberReducer(state: VoterData[], action: HeadAction): VoterData[] {
  switch (action.type) {
    case 'Fill':
      return [...state, ...(action.payload ?? [])];
    case 'Clear':
      return [];
    case 'Delete':
      return state.filter((x) => x.id !== action.payload);
    case 'Add':
      return [...state, action.payload].sort((x, y) =>
        (x.fullName ?? '') > (y.fullName ?? '') ? 1 : -1
      );
    default:
      return state;
  }
}
function voterReducer(state: VoterData[], action: VoterAction): VoterData[] {
  switch (action.type) {
    case 'Fill':
      return [...state, ...(action.payload ?? [])];
    case 'Clear':
      return [];
    case 'Delete':
      return state.filter((x) => x.id !== action.payload);
    default:
      return state;
  }
}
export default function DCCManagementPage() {
  const [position, setPosition] = useState<VoterPosition[]>([]);
  const [dccTotal, setDccTotal] = useState(0);
  const [searchKey, setSearchKey] = useState<string | undefined>();
  const [voterPageCount, setVoterPageCount] = useState<number>(1);
  const [currentVoterPage, setCurrentVoterPage] = useState<number>(1);
  const setBusy = useSetBusy();
  const setMessage = useSetMessage();
  const setCurrentVoterId = useSetCurrentVoterId();
  const [dccsList, dispatchDcc] = useReducer(memberReducer, []);
  const [votersList, dispatchVoter] = useReducer(voterReducer, []);
  useEffect(
    () => {
      initialize();
    },
    // eslint-disable-next-line
    []
  );

  async function initialize() {
    await getPositions();
    dispatchDcc({ type: 'Clear' });
    await getDcc(1);
  }
  async function getPositions() {
    setBusy(true);
    await getVoterPositions()
      .then((res) => {
        if (res !== undefined) {
          setPosition(res);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  async function searchVotersList(key: string | undefined, page: number) {
    setBusy(true);
    await searchForMembers(key, positionLevel.dcc, undefined, page, 50)
      .then((res) => {
        if (res !== undefined) {
          dispatchVoter({ type: 'Fill', payload: res.voters });
          setVoterPageCount(res.pageCount);
          setCurrentVoterPage(page);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }
  async function getDcc(page: number) {
    setBusy(true);
    await getVoterByLevel(positionLevel.dcc, page, 100000000)
      .then((res) => {
        if (res !== undefined) {
          dispatchDcc({ type: 'Fill', payload: res.voters });
          setDccTotal(res.total);
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function searchVoters(key: string) {
    setSearchKey(key);
    dispatchVoter({ type: 'Clear' });
    searchVotersList(key, 1);
  }

  function loadMoreVoter() {
    searchVotersList(searchKey, currentVoterPage + 1);
  }

  function addToVoter(voter: VoterData): void {
    if (voter.position?.name === null) {
      proceedToAddVoter(voter);
    } else {
      setMessage({
        message: 'This voter is already coded. Do you want to proceed?',
        action: 'YESNO',
        onOk: () => {
          proceedToAddVoter(voter);
        },
      });
    }
  }

  function proceedToAddVoter(voter: VoterData): void {
    setBusy(true);
    insertVoterInTree({
      votersId: voter.id,
      votersHeadId: undefined,
      positionId: position.filter((x) => x.level === positionLevel.dcc)[0].id,
    })
      .then(async (res) => {
        if (res !== undefined) {
          setDccTotal((t) => t + 1);
          if (voter.position?.name === null) {
            voter.position = position.filter(
              (x) => x.level === positionLevel.dcc
            )[0];
            voter.positionId = voter.position?.id ?? 0;
          } else {
            voter.position2 = position.filter(
              (x) => x.level === positionLevel.dcc
            )[0];
            voter.positionId2 = voter.position2?.id ?? 0;
          }
          voter.positionId = voter.position?.id ?? 0;
          dispatchVoter({ type: 'Delete', payload: voter.id });
          dispatchDcc({ type: 'Add', payload: voter });
          // setMessage({ message: voter.fullName + ' added.' });
        }
      })
      .catch((err) => {
        setMessage({ message: err.message });
      })
      .finally(() => setBusy(false));
  }

  function deleteVoter(voter: VoterData): void {
    setMessage({
      message: 'Delete ' + voter.fullName,
      type: 'MESSAGE',
      action: 'YESNO',
      onOk: () => {
        setBusy(true);
        deleteVoterInTree(voter.id)
          .then(async (res) => {
            if (res !== undefined) {
              setDccTotal((t) => t - 1);
              dispatchDcc({ type: 'Delete', payload: voter.id });
              // setMessage({ message: voter.fullName + ' deleted.' });
            }
          })
          .catch((err) => {
            setMessage({ message: err.message });
          })
          .finally(() => setBusy(false));
      },
    });
  }
  return (
    <div className='main-container'>
      <div className='main-item-container'>
        <div className='item-container dcc-container div-container no-padding-top-container'>
          <SeachBar search={searchVoters} />
          <div className='item-container dcc-search-voter-container'>
            <div className='item-table-container'>
              <table className='item-table'>
                <thead>
                  <tr>
                    <th></th>
                    <th>Name</th>
                    <th>Position</th>
                    <th>Barangay</th>
                    <th>Precinct</th>
                  </tr>
                </thead>
                <tbody>
                  {votersList.map((voter) => (
                    <tr key={voter.id} id={'voter-' + voter.id}>
                      <td>
                        {voter.position?.name === null && (
                          <FontAwesomeIcon
                            className='add-icon table-icon-button'
                            icon={faPlus as IconProp}
                            onClick={() => addToVoter(voter)}
                          />
                        )}
                      </td>
                      <td>{voter.fullName}</td>
                      <td>
                        <PositionLeader
                          position={voter.position}
                          leader={
                            voter.position?.tag === 'PN'
                              ? voter.pnLeader
                              : voter.jtcLeader
                          }
                          doublePosition={voter.position2?.name !== null}
                        />
                        <PositionLeader
                          position={voter.position2}
                          leader={
                            voter.position2?.tag === 'PN'
                              ? voter.pnLeader
                              : voter.jtcLeader
                          }
                          doublePosition={true}
                          pending={
                            voter.position2?.level === 5 ||
                            voter.position2?.level === 10
                          }
                        />
                      </td>
                      <td>{voter.barangay?.name}</td>
                      <td>{voter.precinctNumber}</td>
                    </tr>
                  ))}
                </tbody>
                {currentVoterPage !== voterPageCount && voterPageCount > 0 && (
                  <tfoot>
                    <tr onClick={loadMoreVoter}>
                      <td colSpan={5} className='load-more' align='center'>
                        Load More
                      </td>
                    </tr>
                  </tfoot>
                )}
              </table>
            </div>
          </div>
        </div>
        <div className='item-container dcc-container div-container no-padding-top-container'>
          <div className='voter-header'>DISTRICT COORDINATORS</div>
          <div className='item-container dcc-member-container'>
            <div className='item-table-container'>
              <table className='item-table'>
                <thead>
                  <tr>
                    <th></th>
                    <th>Name</th>
                    <th>Barangay</th>
                    <th>Precinct</th>
                    <th>Remarks</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {dccsList.map((dcc) => (
                    <tr key={dcc.id} id={'dcc-' + dcc.id}>
                      <td>
                        <FontAwesomeIcon
                          className='remove-icon table-icon-button'
                          icon={faTimes as IconProp}
                          onClick={() => deleteVoter(dcc)}
                        />
                      </td>
                      <td>{dcc.fullName}</td>
                      <td>{dcc.barangay?.name}</td>
                      <td>{dcc.precinctNumber}</td>
                      <td>
                        <VoterRemarks voter={dcc} />
                      </td>
                      <td>
                        <button
                          className='btn-border'
                          onClick={() => {
                            setCurrentVoterId(dcc.id);
                            window.location.href = Routes.Group;
                          }}>
                          View Group
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
          <div className='footer'>
            <div className='total'>
              Total Count : {dccTotal.toLocaleString()}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
