import { ConfigsEnd } from '../endpoints';
import { InvitationConfig } from '../entities/voter/InvitationConfig';
import { httpGet } from './base';

export async function getInvitationConfig(): Promise<
  InvitationConfig[] | undefined
> {
  return await httpGet<InvitationConfig[]>(ConfigsEnd.GetList);
}

export async function getDistributionTemplate(): Promise<string | undefined> {
  return await httpGet<string>(ConfigsEnd.GetTemplate);
}
