import React from 'react';
import { VoterPosition } from '../../entities/voter/VoterPosition';

export default function PositionLeader({
  position,
  leader,
  doublePosition,
  pending,
}: {
  position: VoterPosition | undefined;
  leader: string | undefined;
  doublePosition?: boolean | false;
  pending?: boolean | false;
}) {
  return (
    <div>
      {position?.name !== null && (
        <>
          {pending && 'PENDING FOR '}
          {position?.name}
          {position?.level !== 0 && (position?.level ?? 0) > 5
            ? ' (JTC) '
            : doublePosition
            ? ' (PARTIDO) '
            : ' '}
          {leader && (
            <b>
              <i>by {leader}</i>
            </b>
          )}
        </>
      )}
    </div>
  );
}
