import { jsPDF } from 'jspdf';
import autoTable from 'jspdf-autotable';
import { positionLevel } from '../constant';
import { Attendance } from '../entities/voter/Attendance';
import { InvitationConfig } from '../entities/voter/InvitationConfig';
import { VoterData } from '../entities/voter/VoterData';
import { getInvitationConfig } from '../processors/configs';
import { getVotersMembers } from '../processors/voter';
import { centerText } from './helper';

async function getData(level: number, headId: number): Promise<VoterData[]> {
  return (
    (await getVotersMembers(level, 1, headId, 10000000)
      .then((res) => {
        if (res !== undefined) {
          return res.voters;
        }
        return [];
      })
      .catch((err) => {
        throw err;
      })) ?? []
  );
}

export function printAttendanceReport(voters: Attendance[]) {
  const doc = new jsPDF({
    orientation: 'portrait',
    format: 'legal',
  });
  autoTable(doc, {
    headStyles: {
      fillColor: 'white',
      textColor: 'black',
      lineColor: 'gray',
      lineWidth: 0.1,
      halign: 'center',
      valign: 'middle',
    },
    bodyStyles: {
      fillColor: 'white',
      textColor: 'black',
      lineColor: 'gray',
      lineWidth: 0.1,
      minCellHeight: 15,
      fontSize: 8,
    },
    alternateRowStyles: {
      fillColor: 'white',
    },
    columnStyles: {
      0: { halign: 'right', valign: 'middle' },
      1: { cellWidth: 50, halign: 'left', valign: 'middle' },
      2: { halign: 'center', valign: 'middle' },
      3: { cellWidth: 40, halign: 'center', valign: 'middle' },
      4: { halign: 'center', valign: 'middle' },
      5: { cellWidth: 30 },
    },
    head: [
      ['', 'Full Name', 'Precinct', 'Barangay', 'Date & Time In', 'Signature'],
    ],
    body: voters.map((voter, i) => {
      return [
        (i + 1).toString(),
        voter.voter?.fullName ?? '',
        voter.voter?.precinctNumber ?? '',
        voter.voter?.barangay?.name ?? '',
        voter.loginDate === null
          ? ''
          : new Date(voter.loginDate).toLocaleString(),
      ];
    }),
    didDrawCell: function (data) {
      // if (data.column.index === 5 && data.cell.section === 'body') {
      //   var textPos = data.cell.getTextPos();
      //   doc.addImage(
      //     (voters.filter(
      //       (x) =>
      //         x.voter.fullName ===
      //           (data.row.raw as CellInput[])[1]?.toString() ?? ''
      //     ) ?? [])[0].voter?.signature ?? '',
      //     textPos.x,
      //     textPos.y - 1,
      //     data.cell.height * 2,
      //     data.cell.height
      //   );
      // }
    },
  });

  doc.save('attendance.pdf');
}

export async function printDistributionData(
  voters: VoterData[],
  template: string | undefined,
  fileCnt: number
) {
  return new Promise((resolve) => {
    const doc = new jsPDF({
      orientation: 'portrait',
      format: 'legal',
    });
    doc.setFontSize(8);
    doc.setFont(doc.getFont().fontName, 'normal');

    let topLine: number = 12;
    let leftIndent = 5;
    let top: number = topLine;
    let left: number = leftIndent;
    const imageHeight = 60;
    const imageWidth = 103;
    let blockWith = imageWidth + 1;
    let blockHeight = imageHeight + 1;

    let cnt = 0;

    let voterData: VoterData;
    for (let i: number = 0; i < voters.length; i++) {
      voterData = voters[i];
      if (template) {
        doc.addImage(template, 'JPEG', left, top, imageWidth, imageHeight);
      }
      if (voterData.pnLeader) {
        doc.text(voterData?.pnLeader ?? '', left + 3, top + 5);
      }
      centerText({
        doc,
        y: top + 12,
        width: 62.5,
        fontSize: 7.5,
        text: voterData?.fullName ?? '',
        x: left + 4.5,
      });

      centerText({
        doc,
        y: top + 25.5,
        width: 55,
        fontSize: 7.5,
        text: voterData?.barangay?.name ?? '',
        x: left + 16,
      });

      doc.text(
        `${voterData.cluster ?? ''} - ${voterData?.precinctNumber ?? ''}`,
        left +
          imageWidth -
          doc.getTextWidth(
            `${voterData.cluster ?? ''} - ${voterData?.precinctNumber ?? ''}`
          ) -
          2,
        top + imageHeight - 2
      );

      if (voterData.qrBase64) {
        const imgData = voterData.qrBase64 ?? '';
        doc.addImage(imgData, 'JPEG', left + imageWidth - 30, top + 1, 29, 29);
      }

      if ((cnt + 1) % 2 === 1) {
        top = topLine;
        leftIndent = blockWith + leftIndent;
        left = leftIndent;
        blockWith = blockWith * 2;
      }

      if ((cnt + 1) % 2 === 0) {
        if ((cnt + 1) % 10 !== 0) {
          topLine += blockHeight;
        } else {
          doc.addPage();
          topLine = 12;
        }

        blockWith = blockWith / 2;
        leftIndent = leftIndent - blockWith;
        left = leftIndent;
        top = topLine;
      }

      cnt++;
    }

    doc.save(`distribution_${fileCnt}.pdf`);
    setTimeout(() => {
      resolve('resolved');
    }, 3000);
  });
}

export async function printInvitationReport(
  voters: VoterData[],
  config: InvitationConfig[] | undefined
) {
  const doc = new jsPDF({
    orientation: 'portrait',
    format: 'legal',
  });
  doc.setFont('Arial', 'normal');
  const defConfig: InvitationConfig = {
    day: '',
    month: '',
    time: '',
    ampm: '',
    address: '',
    clusterStart: undefined,
    clusterEnd: undefined,
    barangayStart: undefined,
    barangayEnd: undefined,
    signatory: '',
    signature: undefined,
  };

  const leftIndent = 20;
  let top: number = 10;
  let left: number = leftIndent;
  const line = 5;
  const fontSize = 10;
  const fontSizeUp = fontSize + 2;
  const pageWidth = 220;

  for (let i: number = 0; i < voters.length; i++) {
    const selConfig: InvitationConfig = (config?.filter(
      (x) =>
        (!x.barangayStart ||
          ((x.barangayStart ?? 0) <= (voters[i].barangayId ?? 0) &&
            (x.barangayEnd ?? 0) >= (voters[i].barangayId ?? 0))) &&
        (!x.clusterStart ||
          (+(x.clusterStart ?? '0') <= +(voters[i].cluster ?? '0') &&
            +(x.clusterEnd ?? '0') >= +(voters[i].cluster ?? '0')))
    ) ?? [defConfig])[0];

    doc.setFontSize(fontSize + 1);
    doc.text('Kasamang', left, top);
    left += doc.getTextWidth('Kasamang') + 3;
    doc.setFontSize(fontSizeUp + 1);
    doc.text(voters[i].fullName ?? '', left, top);
    const textWidth = doc.getTextWidth(voters[i].fullName ?? '');
    doc.line(left, top + 1, left + 1 + textWidth, top + 1);

    top += line;
    left = leftIndent;
    doc.setFontSize(fontSize);
    doc.text('BRGY:', left, top);
    left += doc.getTextWidth('BRGY:') + 3;
    doc.setFontSize(fontSizeUp);
    doc.text(voters[i].barangay?.name ?? '', left, top);
    left += doc.getTextWidth(voters[i].barangay?.name ?? '') + 3;
    doc.setFontSize(fontSize);
    doc.text('CLUSTER', left, top);
    left += doc.getTextWidth('CLUSTER') + 3;
    doc.setFontSize(fontSizeUp);
    doc.text(voters[i].cluster ?? '', left, top);
    left += doc.getTextWidth(voters[i].cluster ?? '') + 3;
    doc.setFontSize(fontSize);
    doc.text('PRECINCT No.', left, top);
    left += doc.getTextWidth('PRECINCT No.') + 3;
    doc.setFontSize(fontSizeUp);
    doc.text(voters[i].precinctNumber ?? '', left, top);

    left = leftIndent + 10;
    top += line;
    top += line;
    doc.setFontSize(fontSize);
    doc.text(
      `Ikaw ay malugod na inaanyayahan sa isang pagtitipon sa ika-${
        selConfig?.day ?? '___'
      } ng ${selConfig?.month ?? '__________'} sa ganap ng ${
        selConfig?.time ?? '__:__'
      } ng ${selConfig?.ampm ?? '__________'},`,
      left,
      top
    );

    top += line;
    left = leftIndent;
    doc.setFontSize(fontSize);
    doc.text(
      `Sa ${
        selConfig?.address ??
        '______________________________________________________'
      }.`,
      left,
      top
    );

    left = leftIndent + 10;
    top += line;
    top += line;
    doc.text(
      "Bilang pagkilala sa inyong liderato sa Barangay, Ibig naming kayong maging kabalikat sa mithiing lalo't higit",
      left,
      top
    );

    top += line;
    left = leftIndent;
    doc.text('na mapaunlad ang Syudad ng Navotas.', left, top);

    left = leftIndent + 10;
    top += line;
    top += line;
    doc.text('Salamat at Mabuhay!', left, top);

    left = leftIndent;
    top += line + 3;
    doc.text(
      '(pakidala lamang po ang paanyayang ito at ID sa araw ng pulong)',
      left,
      top
    );

    left = leftIndent;
    top += line;
    top += line;
    doc.setFont('Arial', 'bold');
    doc.text('NOTE:', left, top);
    left += doc.getTextWidth('NOTE:') + 5;
    doc.text('NO PROXY', left, top);
    top += line;
    doc.text('NO VACCINE NO ENTRY', left, top);

    left = pageWidth - 100;

    doc.setFont('Arial', 'normal');
    doc.text('MARAMING SALAMAT PO', left, top - line);
    doc.text(selConfig?.signatory ?? '', left + 20, top + line);

    let imgData: string = '';
    if (selConfig?.signature) {
      imgData = selConfig?.signature ?? '';
      doc.addImage(imgData, 'PNG', left + 5, top - 3, 30, 10);
    }
    top += line;
    if (voters[i].qrBase64) {
      imgData = voters[i].qrBase64 ?? '';
      doc.addImage(imgData, 'JPEG', leftIndent, top, 30, 30);
    }
    if ((i + 1) % 3 !== 0) {
      top += 35;
      doc.line(10, top, pageWidth - 10, top);
    }

    top += 10;
    left = leftIndent;
    if ((i + 1) % 3 === 0) {
      top = 10;
      if (i !== voters.length - 1) doc.addPage();
    }
  }
  doc.save('invitation.pdf');
}

export function printMemberReportSingle(
  voters: VoterData[],
  level: number,
  head?: VoterData | undefined
) {
  const doc = new jsPDF({
    orientation: 'landscape',
    format: 'legal',
  });

  const leader = getPosition(
    (head?.position?.tag === 'PN'
      ? head?.position.level
      : head?.position2?.level) ?? 0
  );
  const member = getPosition(level);

  doc.setFontSize(12).text(`${leader} NAME: ${head?.fullName ?? ''}`, 15, 10);

  autoTable(doc, {
    startY: 15,
    headStyles: {
      fillColor: 'white',
      textColor: 'black',
      lineColor: 'gray',
      lineWidth: 0.1,
      halign: 'center',
    },
    bodyStyles: {
      fillColor: 'white',
      textColor: 'black',
      lineColor: 'gray',
      lineWidth: 0.1,
    },
    alternateRowStyles: {
      fillColor: 'white',
    },
    head: [[`${member} Name`, 'Barangay', 'Precinct', 'Remarks']],
    body: voters.map((voter) => {
      return [
        voter.fullName ?? '',
        voter.barangay?.name ?? '',
        voter.precinctNumber ?? '',
        getRemarks(voter),
      ];
    }),
  });
  end(doc);
}
function getRemarks(voter: VoterData): string {
  return `${
    voter.position2?.name !== null
      ? `${voter.position?.name}${
          voter.position?.level !== 0 && (voter.position?.level ?? 0) > 5
            ? ' (JTC) '
            : ' '
        }${voter.jtcLeader ? `by ${voter.jtcLeader}` : ''}${
          voter.position2?.level === 5
            ? `\r\nPENDING FOR PARTIDO ${voter.position2?.name}`
            : ''
        }`
      : ''
  }`;
}
function getPosition(level: number): string {
  return (
    (level === positionLevel.dcc && 'DCC') ||
    (level === positionLevel.bcc && 'BCC') ||
    (level === positionLevel.purok && 'PUROK LEADER') ||
    (level === positionLevel.liner && 'LINER') ||
    (level === positionLevel.coding && 'CODING') ||
    ''
  );
}
export async function printMemberReport(
  voters: VoterData[],
  headLevel: number,
  memberLevel: number
) {
  const doc = new jsPDF({
    orientation: 'landscape',
    format: 'legal',
  });
  let bodyOf: { head: VoterData; body: any[] }[] = [];
  for (let i: number = 0; i < voters.length; i++) {
    const member = await getData(memberLevel, voters[i].id);
    bodyOf.push({
      head: voters[i],
      body: [
        ...member.map((x, d) => {
          return [
            (d + 1).toString(),
            x.fullName ?? '',
            x.precinctNumber ?? '',
            x.attendance?.loginDate === undefined ||
            x.attendance?.loginDate === null
              ? ''
              : new Date(x.attendance.loginDate).toLocaleString(),
            x.attendance?.logoutDate === undefined ||
            x.attendance?.logoutDate === null
              ? ''
              : new Date(x.attendance.loginDate).toLocaleString(),
            x.contactNumber ?? '',
          ];
        }),
      ],
    });
  }
  doc.setFontSize(10);
  const leader = getPosition(headLevel);
  const member = getPosition(memberLevel);
  for (let l: number = 0; l < bodyOf.length; l++) {
    centerText({
      doc,
      y: 10,
      text: bodyOf[l].head?.barangay?.name ?? '',
      fontSize: 15,
      fontWeight: 'bold',
    });

    doc
      .setFontSize(12)
      .text(`${leader} NAME: ${bodyOf[l].head.fullName ?? ''}`, 15, 30);

    autoTable(doc, {
      startY: 35,
      headStyles: {
        fillColor: 'white',
        textColor: 'black',
        lineColor: 'gray',
        lineWidth: 0.1,
        halign: 'center',
      },
      bodyStyles: {
        fillColor: 'white',
        textColor: 'black',
        lineColor: 'gray',
        lineWidth: 0.1,
      },
      alternateRowStyles: {
        fillColor: 'white',
      },
      columnStyles: {
        0: { halign: 'center' },
        2: { halign: 'center' },
        3: { cellWidth: 60 },
        4: { cellWidth: 60 },
        5: { cellWidth: 60 },
      },
      head: [['', `${member} NAME`, 'PRECINCT NO', 'IN', 'OUT', 'CP NO']],
      body: [...bodyOf[l].body],
    });

    if (l !== bodyOf.length - 1) doc.addPage();
  }
  end(doc);
}

export function printReport(voterToPrint: VoterData[]) {
  const doc = new jsPDF({
    orientation: 'landscape',
    format: 'legal',
  });
  doc.setFontSize(10);
  autoTable(doc, {
    headStyles: {
      fillColor: 'white',
      textColor: 'black',
      lineColor: 'gray',
      lineWidth: 0.1,
      halign: 'center',
    },
    bodyStyles: {
      fillColor: 'white',
      textColor: 'black',
      lineColor: 'gray',
      lineWidth: 0.1,
    },
    alternateRowStyles: {
      fillColor: 'white',
    },
    head: [
      [
        'Full Name',
        'Position',
        'District',
        'Barangay',
        'Cluster',
        'Precinct Number',
      ],
    ],
    body: [
      ...voterToPrint.map((voter) => {
        return [
          voter.fullName ?? '',
          getVoterPosition(voter),
          voter.barangay?.district ?? '',
          voter.barangay?.name ?? '',
          voter.cluster ?? '',
          voter.precinctNumber ?? '',
        ];
      }),
    ],
  });
  end(doc);
}
function getVoterPosition(voter: VoterData): string {
  const leader1 =
    voter.position?.tag === 'PN' ? voter.pnLeader : voter.jtcLeader;
  const leader2 =
    voter.position2?.tag === 'PN' ? voter.pnLeader : voter.jtcLeader;
  return `${
    voter.position?.name !== null
      ? `${voter.position?.name}${
          voter.position?.level !== 0 && (voter.position?.level ?? 0) > 5
            ? ' (JTC) '
            : voter.position2?.name !== null
            ? ' (PARTIDO) '
            : ' '
        }${leader1 && `by ${leader1}`}`
      : ''
  }${
    voter.position2?.name !== null
      ? `\r\n${
          voter.position2?.level === 5 || voter.position2?.level === 10
            ? 'PENDING FOR '
            : ''
        }${voter.position2?.name}${
          voter.position2?.level !== 0 && (voter.position2?.level ?? 0) > 5
            ? ' (JTC) '
            : ' (PARTIDO) '
        }${leader2 && `by ${leader2}`}`
      : ''
  }`;
}
export async function printBCCReport(voter: VoterData[]) {
  const doc = new jsPDF({
    orientation: 'landscape',
    format: 'legal',
  });

  let body: any[] = [];
  let bodyOf: { bcc: VoterData; purok: VoterData; body: any[] }[] = [];
  for (let i: number = 0; i < voter.length; i++) {
    const purok = await getData(positionLevel.purok, voter[i].id);
    for (let j: number = 0; j < purok.length; j++) {
      const liner = await getData(positionLevel.liner, purok[j].id);
      body = [];
      if (liner.length > 0) {
        body.push(
          ...liner.map((x, d) => {
            return [
              (d + 1).toString(),
              x.fullName ?? '',
              x.precinctNumber ?? '',
              '',
              '',
              x.contactNumber ?? '',
            ];
          })
        );
      }
      bodyOf.push({ bcc: voter[i], purok: purok[j], body: body });
    }
  }
  doc.setFontSize(10);
  for (let l: number = 0; l < bodyOf.length; l++) {
    doc.text(`BCC NAME: ${bodyOf[l].bcc.fullName ?? ''}`, 15, 10);
    doc.text(`PUROK NAME: ${bodyOf[l].purok.fullName ?? ''}`, 15, 20);
    autoTable(doc, {
      startY: 27,
      headStyles: {
        fillColor: 'white',
        textColor: 'black',
        lineColor: 'gray',
        lineWidth: 0.1,
        halign: 'center',
      },
      bodyStyles: {
        fillColor: 'white',
        textColor: 'black',
        lineColor: 'gray',
        lineWidth: 0.1,
      },
      alternateRowStyles: {
        fillColor: 'white',
      },
      columnStyles: {
        0: { halign: 'center' },
        2: { halign: 'center' },
        3: { cellWidth: 60 },
        4: { cellWidth: 60 },
        5: { cellWidth: 60 },
      },
      head: [['', 'LINER NAME', 'PRECINCT NO', 'IN', 'OUT', 'CP NO']],
      body: [...bodyOf[l].body],
    });

    if (l !== bodyOf.length - 1) doc.addPage();
  }
  end(doc);
}

function end(doc: jsPDF) {
  var pageCount = doc.getNumberOfPages();
  for (let x: number = 0; x < pageCount; x++) {
    doc.setPage(x);
    let pageCurrent = doc.getCurrentPageInfo().pageNumber;
    doc.setFontSize(9);
    doc.text(`Page ${pageCurrent} of ${pageCount}`, 5, 212);
  }
  doc.save('report.pdf');
}
